@import "../../../../../assets/style/mixins";

.documents {
  h4 {
    margin: 0;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: var(--green);
  }

  .cardWrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .card {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      background: #213054;
      border-radius: 20px;
      padding: 20px;
      width: 460px;
      margin-top: 20px;

      .iconDocuments {
        background: rgba(35, 188, 250, 0.15);
        border-radius: 10px;
        padding: 25px;
      }

      .text {
        display: flex;
        flex-direction: column;
        margin-left: 20px;

        .title {
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 120%;
          color: var(--white);
        }

        .description {
          font-style: normal;
          font-weight: 500;
          font-size: 17px;
          line-height: 21px;
          color: rgba(251, 251, 251, 0.5);
          margin-top: 10px;
        }
      }
    }
  }
}