@import '../../../assets/style/mixins';

.title {
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: var(--green);
  margin: 0 20px;
  text-align: center;

  @include onTablet {
    text-align: left;
    margin-left: 25px;
  }

  @include upHorizontalTablet {
    font-size: 24px;
    line-height: 29px;
  }

  @include onNotebook {
    text-align: left;
  }

  @include onFull {
    margin: 30px 30px 10px;
    font-size: 24px;
  }
}

.smartPage {
  margin-top: 20px;

  @include onNotebook {
    margin-top: 0;
  }

  @include onFull {
    margin-top: 0;
  }
}

.PanelBlock {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 360px;
  padding: 0 20px 19px;

  @include onTablet {
    padding: 10px;
    width: 100%;
  }

  @include onNotebook {
    width: 100%;
    padding: 20px 10px;
    flex-direction: row;
  }

  @include onFull {
    width: 100%;
    margin-top: 0;
  }

  .balance {
    flex: 1 1 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  
    @include onTablet {
      flex-wrap: nowrap;
    }
  
    @include onNotebook {
      flex-direction: row;
      flex-wrap: nowrap;
      flex: 1 1 50%;
    }

    @include onFull {
      flex-direction: row;
      flex-wrap: nowrap;
      flex: 1 1 50%;
    }
  }
  
  .otherBlocks {
    flex: 1 1 100%;
    display: flex;
    flex-direction: column;

    @include onTablet {
      flex-direction: row;
    }
  
    @include onNotebook {
      flex: 0 0 50%;
      flex-direction: row;
    }
  
    @include onFull {
      flex: 0 0 50%;
      flex-direction: row;
    }
  
    .contracts {
      flex: 1 1 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      margin-top: 10px;
    
      @include onTablet {
        flex-wrap: nowrap;
      }

      @include upHorizontalTablet {
        flex-direction: column;
        flex: 0 0 50%;
        padding-right: 20px;
      }
    
      @include onNotebook {
        flex-direction: row;
        flex-wrap: wrap;
        padding: 0;
      }

      @include onFull {
        flex-direction: row;
        flex-wrap: wrap;
        margin: 0;
        flex: 1 1 50%;
      }
    }
    
    .BuyContracts {
      padding-bottom: 20px;
      flex: 1 1 100%;
    
      @include onTablet {
        padding: 10px;
        flex: 0 0 50%;
      }
    
      @include onNotebook {
        padding: 20px 10px 0;
        flex: 1 1 50%;
      }

      @include onFull {
        padding: 20px;
        flex: 1 1 50%;
      }
    }
  }
}
