@import '../../../assets/style/mixins';

.PaymentMethodPage {
  width: 320px;
  background: rgba(33, 48, 84, 1);
  padding: 50px 10px 25px 10px;
  border-radius: 10px;
  position: relative;

  @include onTablet {
    width: 700px;
    padding: 10px 40px;
  }

  @include onNotebook {
    width: 700px;
    padding: 10px 40px;
  }

  @include onFull {
    width: 700px;
    max-width: 700px;
    padding: 10px 40px;
  }

  .title {
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    margin-bottom: 30px;

    @include onTablet {
      font-size: 20px;
      line-height: 24px;
      margin-bottom: 40px;
    }
  
    @include onNotebook {
      font-size: 20px;
      line-height: 24px;
    }
  
    @include onFull {
      font-size: 20px;
      line-height: 24px;
    }
  }

  .PaymentMethodList {
    display: flex;
    flex-wrap: wrap;

    @include onTablet {
      margin-bottom: 25px;
    }

    @include onNotebook {
      margin-bottom: 25px;
    }
  
    @include onFull {
      margin-bottom: 25px;
    }

    .PaymentMethodItem {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 48px;
      margin-bottom: 15px;
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      border: 1px solid #39D2D1;
      border-radius: 15px;

      @include onTablet {
        flex: 1 1 45%;
        margin: 0 5px 15px;
      }
  
      @include onNotebook {
        flex: 1 1 45%;
        margin: 0 5px 15px;
      }
    
      @include onFull {
        margin: 0 5px 15px;
        flex: 1 1 45%;
      }

      .icon {
        width: 14px;
        height: 14px;
        margin-right: 5px;
      }

      .name {

      }
    }
  }

  .button {
    width: 100%;
    height: 48px;
    margin: 0 auto;

    @include onTablet {
      margin: 0 auto 40px;
      height: 68px;
    }

    @include onNotebook {
      margin: 0 auto 40px;
      height: 68px;
    }
  
    @include onFull {
      margin: 0 auto 40px;
      height: 68px;
    }
  }
}