@import '../../../../assets/style/mixins';

p {
  margin: 0;
}

.buyContracts {
  background-color: var(--darkBlue);
  height: 100%;
  width: 100%;
  border-radius: 15px;
  color: var(--white);
  padding: 10px 20px 20px;
  display: flex;
  flex-direction: column;
  user-select: none;

  @include onTablet {
    padding: 10px 20px 20px;
  }

  @include onNotebook {
    max-height: 365px;
  }

  @include onFull {
    max-height: 502px;
    padding: 10px 30px;
  }

  .header {
    flex: 1 1 1fr;
    display: flex;
    text-align: center;
    flex-direction: column;
    border-bottom: 1px solid rgb(14%, 74%, 98%, 0.2);
    margin-bottom: 10px;

    @include upHorizontalTablet {
      margin-bottom: 0;
      padding-bottom: 20px;
    }

          
    @include onNotebook {
      padding-bottom: 10px;
    }

    .infoString {
      font-size: 14px;
      display: flex;
      color: var(--blueDark);
      justify-content: center;
      align-items: center;

      @include onNotebook {
        font-size: 14px;
        line-height: 150%;
      }

      .infoIcon {
        margin-right: 10px;
        div svg {
          width: 20px;
          height: 20px;
      
          path {
            fill: #23BCFA;
          }
        }
      }
    }

    .subtitle {
      display: flex;
      flex-direction: column;
      font-size: 13px;
      line-height: 13px;
      text-align: center;
      letter-spacing: 0.05em;
      
      color: rgba(251, 251, 251, 0.6);
      
      margin-bottom: 3px;
      padding: 0;
    }

    .name {
      font-family: Montserrat;
      font-size: 18px;
      font-style: normal;
      font-weight: 700;
      line-height: 22px;
      letter-spacing: 0.05em;
      text-align: center;

      @include upHorizontalTablet {
        font-family: Montserrat;
        font-size: 28px;
        font-style: normal;
        font-weight: 700;
        line-height: 27px;
        letter-spacing: 0.05em;
        text-align: center;
      }
      
      @include onNotebook {
        font-family: Montserrat;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 22px;
        letter-spacing: 0.05em;
        text-align: center;
      }
    }
  }

  .main {
    flex: 1 1 50px;
    display: flex;
    font-size: 14px;
    align-items: center;
    justify-content: space-around;

    @include onTablet {  
      margin-bottom: 0;
    }

    @include upHorizontalTablet {
      align-items: center;
      justify-content: center;
    }

    @include onNotebook {
      font-size: 14px;
      line-height: 17px;
    }

    .reverseIcon {
      margin: 0 20px;

      div svg {
        width: 16px;
        height: 14px;
    
        path {
          fill: #23BCFA;
        }
      }
    }
  }

  .chooseAmount {
    text-align: center;
    font-size: 14px;
    color: #ABE7FF;
    margin-bottom: 5px;
  }

  .buttonBlock {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex: 1 1 40%;


    .rules {
      display: flex;
      margin-bottom: 10px;



      .checkBoxWrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        flex: 0 0 23px;
        width: 23px;
        height: 23px;
        border: 2px solid var(--blueDark);
        border-radius: 6px;
        cursor: pointer;
        margin-right: 5px;
      
        .checkBox {
          position: relative;
          margin: 0;
          opacity: 0;
      
          &:before {
            content: '';
            display: inline-block;
            background-repeat: no-repeat;
            background-position: center center;
            position: absolute;
          }
      
          &:checked::before {
            background-image: url(../../../../assets/icons/Subtract.svg)
          }

          &:checked {
            opacity: 1;
            width: 100%;
            height: 100%;
          }
        }
      }

      .rulesText {
        flex: 1 1 1fr;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 140%;
        margin-bottom: 2px;

        @include upHorizontalTablet {
          font-size: 14px;
          line-height: 140%;
        }

        @include onNotebook {
          font-size: 11px;
          line-height: 140%;
          margin-bottom: 0;
        }

        span {
          font-size: 14px;
          color: var(--blueDark);

          @include onNotebook {
            font-size: 11px;
            line-height: 140%;
          }
        }
      }
    }

    .button {
      height: 50px;
      display: flex;
      border-radius: 15px;
      cursor: pointer;
      transition: 300ms;

      &:hover {
        box-shadow: 0 0 10px var(--blueDark);
        transform: translateY(-3px);
      }

      &:active {
        transform: scale(0.95);
      }

      @include onTablet {
      }
    
      @include onNotebook {
      }
    
      @include onFull {
        height: 70px;
      }

      .left {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 14px;
        flex: 0 0 60%;
        background-color: #00AEAC;
        border-top-left-radius: 15px;
        border-bottom-left-radius: 15px;
      }

      .right {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 10px;
        flex: 0 0 40%;
        background: var(--gradienBlack);
        border-top-right-radius: 15px;
        border-bottom-right-radius: 15px;

        div {
          display: flex;

          span {
            font-weight: bold;
            font-size: 12px;
            margin-right: 5px;
          }
        }
      }
    }
  }
}



.DNCCoin {
  div svg {
    width: 17px;
    height: 17px;

    path {
      fill: #23BCFA;
    }
  }
}

.contractIcon {
  div svg {
    width: 16px;
    height: 20px;

    path {
      fill: #23BCFA;
    }
  }
}

.fakeButton {
  height: 50px;
  display: flex;
  border-radius: 15px;
  cursor: pointer;
  transition: 300ms;
  position: relative;
  overflow: hidden;

  .wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: grey;
    opacity: 0.5;
  }

  @include onTablet {
  }

  @include onNotebook {
  }

  @include onFull {
    height: 70px;
  }

  .left {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    flex: 0 0 60%;
    background-color: #00AEAC;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
  }

  .right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 10px;
    flex: 0 0 40%;
    background: var(--gradienBlack);
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;

    div {
      display: flex;

      span {
        font-weight: bold;
        font-size: 12px;
        margin-right: 5px;
      }
    }
  }
}
