.customSlider h1 {
  font-style: normal;
  font-weight: 600;
  font-size: 64px;
  line-height: 140%;
  color: #FBFBFB;
  margin-left: 80px;
  padding-bottom: 40px; }
.customSlider svg {
  width: 80%;
  height: 80%; }
.customSlider .slick-arrow {
  background: rgba(35, 188, 250, 0.1);
  border-radius: 15px;
  width: 50px;
  height: 50px;
  position: absolute;
  bottom: 0;
  top: 95%;
  left: 6%;
  z-index: 99; }
  .customSlider .slick-arrow:hover {
    background: rgba(35, 188, 250, 0.6); }
    .customSlider .slick-arrow:hover:before {
      color: #0B2738; }
  .customSlider .slick-arrow:before {
    font-family: sans-serif;
    display: block;
    margin: 0 auto;
    color: rgba(35, 188, 250, 0.6); }
.customSlider .slick-next {
  left: 11%; }
.customSlider .slick-dots {
  text-align: end;
  right: 5%;
  top: 93%; }
  .customSlider .slick-dots li {
    margin: 0 10px; }
  .customSlider .slick-dots li button {
    border-radius: 50%;
    width: 8px;
    height: 8px;
    background: rgba(255, 255, 255, 0.4); }
    .customSlider .slick-dots li button:before {
      display: none; }
  .customSlider .slick-dots .slick-active button {
    background: var(--blueDark);
    border-radius: 20px;
    width: 32px;
    transition: 300ms; }

@media screen and (max-width: 1700px) {
  .customSlider h1 {
    font-weight: 600;
    font-size: 46px;
    line-height: 140%; } }
@media screen and (max-width: 1500px) {
  .customSlider .slick-next {
    left: 14%; } }
.customSlider .slick-dots {
  right: 0;
  bottom: 0;
  text-align: end;
  display: flex !important; }

@media screen and (min-width: 1025px) {
  .customSlider {
    padding-bottom: 100px;
    transform: translate(0, -10px); }
    .customSlider svg {
      width: 80%;
      height: auto;
      margin-bottom: 26px; }
    .customSlider h1 {
      margin: 0;
      padding: 0;
      font-size: 32px;
      line-height: 132%; }
    .customSlider .sliderItem {
