      right: 35px;
      bottom: 17px;
      width: auto;
      align-items: center;
      top: initial;
      display: flex !important;
      justify-content: flex-end; }
      .customSlider .slick-dots li {
        width: auto;
        height: auto;
        margin: 0 6px; }
        .customSlider .slick-dots li button {
          margin: 0;
          width: 5px;
          padding: 0;
          height: 5px;
          border-radius: 50%;
          background: rgba(255, 255, 255, 0.4); }
      .customSlider .slick-dots .slick-active button {
        width: 20px; }
    .customSlider .slick-arrow {
      bottom: 0;
      z-index: 4;
      width: 40px;
      height: 40px;
