      transform: translate(0, 0); } }
@media screen and (min-width: 1240px) {
  .customSlider {
    padding-bottom: 60px;
    transform: translate(0, -20px); }
    .customSlider svg {
      width: 67.5%;
      height: auto;
      margin-bottom: 26px; }
    .customSlider h1 {
      font-size: 46px;
      line-height: 140%; }
    .customSlider .sliderItem {
      padding: 40px 40px 0 60px; }
    .customSlider .slick-prev {
      left: 60px; }
    .customSlider .slick-next {
      left: 110px; } }
@media screen and (min-width: 1600px) {
  .customSlider {
    padding-bottom: 90px;
    transform: translate(0, -20px); }
    .customSlider h1 {
      font-size: 64px;
      line-height: 140%; }
    .customSlider svg {
      width: 65%;
      margin-bottom: 40px; }
    .customSlider .sliderItem {
      padding: 40px 40px 0 80px; }
    .customSlider .slick-arrow {
      width: 50px;
      height: 50px; }
    .customSlider .slick-prev {
      left: 80px; }
    .customSlider .slick-next {
      left: 140px; }
    .customSlider .slick-dots {
      right: 38px;
      bottom: 22px; }
      .customSlider .slick-dots li {
        margin: 0 10px; }
        .customSlider .slick-dots li button {
          width: 8px;
          height: 8px; }
      .customSlider .slick-dots .slick-active button {
