@import '../../../../assets/style/mixins';

.wrapper {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  @include onTablet {
    margin-left: 80px;
  }

  @include onNotebook {
    margin-left: 80px;
  }

  @include onFull {
    margin-left: 125px;
  }
}

header {
  display: flex;
  width: 100%;
  height: 62px;
  margin: 0 auto;
  padding: 0 20px;
  border-left: none;
  color: var(--white);
  align-items: center;
  box-sizing: border-box;
  background: var(--background);
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;

  .notificationsBlock {
    position: relative;

    @include onMobile {
      display: none;
    }

    .notificationCount {
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 17px;
      color: #FBFBFB;
      position: absolute;
      top: -5px;
      right: 20px;
    }
  }

  @include onTablet {
    height: 72px;
  }

  @include onNotebook {
    height: 72px;
  }

  @include onFull {
    height: 100px;
  }
}

.profile {
  display: flex;
  align-items: center;

  a {
    flex: 0 0 25%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.profilePhoto {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 1px solid #23BCFA;
}

.profile span {
  padding-left: 8px;
  color: var(--white);
}

.smallLogoIcon {
  flex: 0 0 50%;

  @include onTablet {
    display: none;
  }
}

.smallLogoIcon div {
  display: flex;
  align-items: center;
}

.navigation {
  flex: 0 0 25%;
  display: flex;
  justify-content: flex-end;

  @include onTablet {
    display: none;
  }

  @include upHorizontalTablet {
    display: none;
  }
}

.navigation div {
  display: flex;
  align-items: center;
}

.otherInformation {
  display: none;
}

.currencyExchange {
  display: none;
}

.arrowIcon {
  display: none;
}

.language {
  display: none;
}

.notificationsIcon {
  display: none;
}

.leftPanel {
  display: none;
}

.arrowToolsIcon {
  display: none;
}

.extraText {
  display: none;
}

.CoinIcon {
  width: 18px;
  height: 18px;
  margin-right: 8px;
}

.DNSCoinIcon {
  height: 18px;
  width: 18px;
  margin-right: 5px;
}

.notificationsIconMobile {
  margin-right: 23px;
}

@include onTablet {
  .info {
    display: flex;
    margin-right: 25px;

    .arrowToolsIcon {
      display: none;
    }
  }

  .currencyExchange {
    display: flex;
  }

  .currencyExchangeRZM {
    display: none;
  }

  .otherInformation {
    font-size: 14px;
    display: flex;
    color: var(--white);
    margin-right: 30px;
    align-items: center;
  }

  .calendarIcon {
    margin-right: 8px;
    align-items: center;
    height: 30px;
  }

  .date {
    display: flex;
    flex-direction: column;
  }

  .date .subText {
    font-size: 10px;
  }

  .currencyExchangeDNC {
    display: flex;
    color: var(--white);
    align-items: center;
  }

  .vectorIcon {
    margin-left: 10px;
    margin-right: 10px;
  }

  .logo {
    display: none;
  }

  .tetherICon {
    width: 18px;
    height: 18px;
    margin-right: 5px;
  }

  .arrowIcon {
    margin-left: 8px;
    display: block;
  }

  .rightPanel {
    display: flex;
    margin-right: 25px;
    align-items: center;
  }

  .language {
    color: var(--blue);
    display: inline-block;
    margin-right: 30px;
  }

  .leftPanel {
    display: flex;
    align-items: center;
  }

  .notificationsIcon {
    display: block;
    margin-right: 30px;
    height: 22px;
  }

  .arrowToolsIcon {
    display: block;
    margin-left: 8px;
  }

  .weather {
    display: none
  }

  .cloudIcon {
    display: none
  }

  .clockIcon {
    display: none
  }

  .time {
    display: none
  }
}

@include upHorizontalTablet {
  .info {
    display: flex;
    margin-right: 25px;
  }

  .currencyExchange {
    display: flex;
  }

  .currencyExchangeRZM {
    display: none;
    cursor: pointer;
  }

  .currencyExchangeDNC {
    display: none;
    cursor: pointer;
  }

  .otherInformation {
    font-size: 14px;
    display: flex;
    color: var(--white);
    margin-right: 30px;
    align-items: center;
  }

  .calendarIcon {
    margin-right: 8px;
    align-items: center;
    height: 30px;
  }

  .date {
    display: flex;
    flex-direction: column;
  }

  .date .subText {
    font-size: 10px;
    color: rgba(255, 255, 255, 0.6);
  }

  .currencyExchangeDNC {
    display: flex;
    color: var(--white);
    align-items: center;
  }

  .vectorIcon {
    margin-left: 10px;
    margin-right: 10px;
  }

  .logo {
    display: none;
  }

  .tetherICon {
    width: 18px;
    height: 18px;
    margin-right: 5px;
  }

  .arrowIcon {
    margin-left: 8px;
    display: block;
  }

  .rightPanel {
    display: flex;
    margin-right: 25px;
    align-items: center;
  }

  .language {
    color: var(--blue);
    display: inline-block;
    margin-right: 30px;
  }

  .leftPanel {
    display: flex;
    align-items: center;
  }

  .notificationsIcon {
    display: block;
    margin-right: 30px;

    div svg {
      width: 23px;
      height: 26px;
      cursor: pointer;

      path {
        fill: rgba(22%, 82%, 82%, 0.4);
      }


      &:hover {
        path {
          fill: rgba(22%, 82%, 82%, 1);;
        }
      }
    }
  }

  .arrowToolsIcon {
    display: block;
    margin-left: 8px;
  }

  .weather {
    display: none
  }

  .cloudIcon {
    display: none
  }

  .clockIcon {
    display: none;
  }

  .time {
    display: none;
  }

  .info {
    display: flex;
  }

  .extraText {
    display: block;
  }

  .currencyExchange {
    flex: 1 1 30%;
    display: flex;
    justify-content: center;;
  }

  .otherInformation {
    display: flex;
    flex: 1 1 60%;
    justify-content: center;
    color: var(--white);
  }

  .leftPanel {
    flex: 0 0 65%;
    display: flex;
    justify-content: space-between;

  }

  .currencyExchangeRZM {
    display: flex;
    color: var(--white);
    align-items: center;
    margin-left: 25px;
  }

  .arrowToolsIcon {
    display: none;
  }

  .weather {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-right: 20px;
  }

  .cloudIcon {
    display: flex;
    align-items: center;
    margin-right: 8px;
  }

  .weather .subText {
    font-size: 10px;
    color: rgba(255, 255, 255, 0.6);
  }

  .clockIcon {
    display: flex;
    align-items: center;
    margin-right: 8px;
  }

  .time {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-right: 20px;
  }

  .time .subText {
    font-size: 10px;
    color: rgba(255, 255, 255, 0.6);
  }
}

@include onFull {
  .otherInformation {
    font-size: 18px;
    font-weight: 500;
    line-height: 22px;
    display: flex;
    color: var(--white);
    margin-right: 30px;
    align-items: center;

    .info {
      align-items: center;
    }

    .cloudIcon {
      div {
        height: 30px;

        svg {
          width: 42px;
          height: 30px;
        }
      }
    }

    .clockIcon {
      margin-left: 10px;

      div {
        height: 30px;

        svg {
          width: 32px;
          height: 32px;
        }
      }
    }

    .calendarIcon {
      margin-left: 10px;

      div {
        height: 32px;

        svg {
          width: 32px;
          height: 32px;
        }
      }
    }
  }

  .date .subText {
    font-family: Montserrat;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 15px;
  }

  .currencyExchange {
    align-items: center;

    .currencyExchangeDNC {

      .dncFirstPart, .dncSecondPart {
        display: flex;
        justify-content: center;
        text-align: center;

        .extraText {
          line-height: 28px;
        }
      }

      .dncSecondPart {
        display: flex;
        justify-content: space-around;
      }
    }

    .DNSCoinIcon {
      height: 28px;
      width: 28px;
    }

    .vectorIcon {
      margin-right: 10px;

      div svg {
        width: 22px;
        height: 19px;
      }
    }

    .tetherICon {
      margin-right: 10px;
      height: 28px;
      width: 28px;

      div svg {
        height: 28px;
        width: 28px;
      }
    }

    .CoinIcon {
      margin-right: 10px;
      height: 28px;
      width: 28px;
    }
  }
}

@media (min-width: 768px) {

  header {
    padding: 0;
  }

  .leftPanel {
    .otherInformation {
      margin-left: 4px;

      .info {
        margin-right: 55px;
      }
    }
  }

  .rightPanel {
    margin-right: 59px;

    .language {
      font-size: 14px;
      color: #23BCFA;
    }

    .profile {
      span {
        font-size: 14px;
      }

      .arrowIcon {
        margin-left: 6px;
      }
    }
  }

}

@media (min-width: 1024px) {

  .leftPanel {
    .otherInformation {
      margin-left: 8px;

      .info {
        margin-right: 23px;

        .cloudIcon {
          display: none;
        }

        .weather {
          display: none;
        }

        .clockIcon {
          display: none;
        }

        .time {
          display: none;
        }

        .date {
          width: max-content;
        }
      }

      .currencyExchange {
        svg {
          display: flex;
        }

        .dncFirstPart {
          display: flex;
          width: max-content;
        }

        .dncSecondPart {
          display: flex;
          width: max-content;
        }
      }

    }
  }

}

@media (min-width: 1240px) {

  .leftPanel {
    .otherInformation {
      margin-left: 14px;

      .info {
        margin-right: 43px;

        .cloudIcon {
          display: flex;
        }

        .weather {
          display: flex;
          margin-right: 30px;
        }

        .clockIcon {
          display: flex;
        }

        .time {
          display: flex;
          margin-right: 30px;
        }
      }

      .currencyExchange {
        justify-content: flex-start;
      }

      .currencyExchangeRZM {
        margin-left: 30px;
      }
    }
  }

}

@media (min-width: 1600px) {

  .wrapper {
    margin-left: 125px;
  }

  .leftPanel {
    .otherInformation {
      margin-left: 32px;
      margin-right: 60px;

      .info {
        margin-right: 70px;

        .weather {
          margin-right: 42px;
        }

        .time {
          margin-right: 42px;
        }
      }
    }
  }

  .rightPanel {
    margin-right: 80px;

    .language {
      font-size: 18px;
      margin-right: 36px;
    }

    .notificationsIcon {
      margin-right: 40px;
    }

    .profile {
      .profilePhoto {
        width: 60px;
        height: 60px;
      }

      span {
        font-size: 18px;
        padding-left: 16px;
      }

      .arrowIcon {
        margin-left: 10px;
      }
    }
  }

}