.inputWrapper {

  input {
    background: transparent;
    outline: none;
    border: none;
    border-bottom: solid 1px rgba(255, 255, 255, 0.2);
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    color: var(--white);
    width: 100%;

    &:empty {
      background-color: transparent;
    }
  }

  input:-webkit-autofill {
    transition: all 5000s ease-in-out 0s;
  }
}