.rejectContract {
  background: rgba(33, 48, 84, 1);
  width: 320px;
  padding: 15px;
  color: white;
  border-radius: 25px;

  .headerBlock {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgba(35, 188, 250, 1);
    margin-bottom: 15px;

    .title {
      font-size: 26px;
      font-style: normal;
      font-weight: 600;
      line-height: 32px;
      text-align: left;
    }

    .rejectIcon {
      cursor: pointer;
    }
  }

  .info {
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    margin-bottom: 15px;
  }

  .block {
    display: flex;
    align-items: center;
    margin-bottom: 10px;

    .checkbox {
      flex: 1 1 35px;
    }

    .rule {
      flex: 1 1 300px;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      text-align: left;
    }
  }

  .buttonBlock {
    height: 47px;
    width: 290px;
    border-radius: 10px;
  }
}


.checkBoxWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 23px;
  height: 23px;
  border: 2px solid var(--blueDark);
  border-radius: 6px;
  cursor: pointer;
  margin-right: 5px;

  .checkBox {
    flex: 0 0 23px;
    cursor: pointer;
    position: relative;
    margin: 0;
    opacity: 0;

    &:before {
      content: '';
      display: inline-block;
      background-repeat: no-repeat;
      background-position: center center;
      position: absolute;
    }

    &:checked::before {
      background-image: url(../../../assets/icons/Subtract.svg)
    }
    &:checked {
      opacity: 1;
      width: 100%;
      height: 100%;
    }
  }
}