@import '../../../../assets/style/mixins';

.panel {
  display: none;
}

.mobileText {
  margin: 0;
  padding: 0;
}

.contract {
  height: 104px;
  background-color: #213054;
  border-radius: 10px;
  font-size: 12px;
  margin-bottom: 10px;
  color: white;

  .mobileText {
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    color: rgba(255, 255, 255, 0.6);
  }

  .span {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
  }

  .arrow {
    display: none;
  }

  .firstPart {
    display: flex;
    align-items: center;
    height: 52px;

    .contractName {
      flex: 0 0 75%;
      display: flex;
      align-items: center;
      font-family: Montserrat;
      font-weight: 600;
      font-size: 14px;
      line-height: 17px;

      .contractIcon {
        margin-left: 20px;
        margin-right: 10px;
        
        div svg {
          width: 14px;
          height: 17px;

          path {
            fill: #23BCFA;
          }
        }
      }

      .Gold {
        div svg {
          width: 14px;
          height: 17px;
          
          path {
            fill: #F4C41A;
          }
        }
      }

      .Mango {
        div svg {
          width: 14px;
          height: 17px;
          
          path {
            fill: rgb(96%, 60%, 10%, 1);
          }
        }
      }
    }

    .amount {
      display: flex;
      flex-direction: column;
      position: relative;
      
      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid rgba(255, 255, 255, 0.2);
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }

      span {
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
      }
    }
  }

  .secondPart {
    background-color: rgb(14%, 74%, 98%, 0.8);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 52px;

    .price {
      margin-left: 20px;
      display: flex;
      flex-direction: column;
      position: relative;
      
      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 8px;
        border-top: 1.5px solid rgba(255, 255, 255, 0.2);
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }
    }

    .income {
      display: flex;
      flex-direction: column;
      position: relative;
      
      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid rgba(255, 255, 255, 0.2);
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }
    }

    .profitability {
      margin-right: 20px;
      display: flex;
      flex-direction: column;
      position: relative;
      
      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid rgba(255, 255, 255, 0.2);
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }
    }
  }
}

.span {
  font-weight: bold;
  font-size: 14px;
}

@include onTablet {
  .panel {
    display: flex;
  }

  .contractName {
    flex: 0 0 40%;
  }

  .amount {
    flex: 0 0 10%;
  }

  .price {
    flex: 0 0 15%;
  }

  .income {
    flex: 0 0 15%;
  }

  .profitability {
    flex: 0 0 20%;
  }

  .mobileText {
    display: none;
  }

  .contract {
    display: flex;
    height: 57px;
    border-radius: 15px;
    overflow: hidden;
  
    .firstPart {
      height: 60px;
      flex: 0 0 50%;
  
      .contractName {
        flex: 0 0 75%;
      }

      .amount {
        flex: 0 0 25%;
        display: flex;
        
        &::before {
          top: 8px;
        }
      }
    }
  
    .secondPart {
      flex: 0 0 50%;
      background-color: inherit;
      align-items: center;
      justify-content: space-between;
      height: 60px;

      .price {
        flex: 0 0 30%;
        margin-left: 0;
        display: flex;
        flex-direction: column;
      }
  
      .income {
        flex: 0 0 30%;
        &::before {
          top: 8px;
        }
      }
  
      .profitability {
        margin-right: 0;
        flex: 0 0 20%;
        
        &::before {
          top: 8px;
        }
      }

      .arrow {
        flex: 0 0 20%;
        display: block;

        .arrowWrapper {
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 37px;
          height: 37px;
          background: rgb(14%, 74%, 98%, 0.15);
          border-radius: 10px;
        }
      }
    }
  }
  
  .span {
    font-weight: bold;
    font-size: 14px;
  }
}

@include upHorizontalTablet {
  .contract {
    height: 62px;
  
    .firstPart {
      .contractName {
        span {
          font-style: normal;
          font-weight: 600;
          font-size: 18px;
          line-height: 22px;
        }
      }
      .amount {
        .span {
          font-style: normal;
          font-weight: 600;
          font-size: 18px;
          line-height: 22px;
        }
      }
    }
  
    .secondPart {
      .arrow {
        .arrowWrapper {
          width: 42px;
          height: 42px;
        }
      }
    }
    .span {
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
    }
  }
}

@include onNotebook {
  .panel {
    display: flex;
  }

  .contractName {
    flex: 0 0 30%;
  }

  .amount {
    flex: 0 0 20%;
  }

  .price {
    flex: 0 0 15%;
  }

  .income {
    flex: 0 0 15%;
  }

  .profitability {
    flex: 0 0 20%;
  }

  .mobileText {
    display: none;
  }

  .contract {
    display: flex;
    height: 62px;
    border-radius: 15px;
    overflow: hidden;
  
    .firstPart {
      height: 60px;
      flex: 0 0 50%;
  
      .contractName {
        flex: 0 0 70%;
      }

      .amount {
        flex: 0 0 30%;
        display: flex;

        span {
          font-style: normal;
          font-weight: 600;
          font-size: 18px;
          line-height: 22px;
        }
        
        &::before {
          top: 8px;
        }
      }
    }
  
    .secondPart {
      flex: 0 0 50%;
      background-color: inherit;
      align-items: center;
      justify-content: space-between;
      height: 60px;

      .price {
        flex: 0 0 30%;
        margin-left: 0;
        display: flex;
        flex-direction: column;
      }
  
      .income {
        flex: 0 0 30%;
        &::before {
          top: 8px;
        }
      }
  
      .profitability {
        margin-right: 0;
        flex: 0 0 20%;
        
        &::before {
          top: 8px;
        }
      }

      .arrow {
        flex: 0 0 20%;
        display: block;

        .arrowWrapper {
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 37px;
          height: 37px;
          background: rgb(14%, 74%, 98%, 0.15);
          border-radius: 10px;
        }
      }
    }
  }
  
  .span {
    font-weight: bold;
    font-size: 14px;
  }
}

@include onFull {
  .mobileText {
    display: none;
  }

  .contract {
    display: flex;
    height: 89px;
    border-radius: 20px;

    .span {
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;
    }
  
    .firstPart {
      height: 89px;
      flex: 0 0 50%;
  
      .contractName {
        flex: 0 0 75%;
        padding-left: 50px;

        span {
          font-weight: 600;
          font-size: 24px;
          line-height: 29px;
        }

        .contractIcon {
          div svg {
            width: 20px;
            height: 24px;
          }
        }
      }

      .amount {
        flex: 0 0 25%;
        display: flex;
        
        &::before {
          top: 14px;
        }

        .span {
          font-size: 24px;
        }
      }
    }
  
    .secondPart {
      flex: 0 0 50%;
      background-color: inherit;
      align-items: center;
      justify-content: space-between;
      height: 89px;

      .price {
        flex: 0 0 30%;
        margin-left: 0;
        display: flex;
        flex-direction: column;
      }
  
      .income {
        flex: 0 0 30%;
        &::before {
          top: 14px;
        }
      }
  
      .profitability {
        margin-right: 0;
        flex: 0 0 20%;
        
        &::before {
          top: 14px;
        }
      }

      .arrow {
        flex: 0 0 20%;
        display: block;

        .arrowWrapper {
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 59px;
          height: 59px;
          background: rgb(14%, 74%, 98%, 0.15);
          border-radius: 15px;
          transition: 300ms;

          &:hover {
            background: rgb(14%, 74%, 98%, 0.4);
          }

          .arrowIcon {
            div svg {
              width: 11px;
              height: 22px;

            }
          }
        }
      }
    }
  }
}