@import "../../../../../assets/style/mixins";

.inheritance {
  &_itemBlock {
    &:last-child {
      margin-top: 20px;
    }

    h4 {
      margin-bottom: 20px;
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;
      color: var(--green);
    }

    .form {
      background: #213054;
      border-radius: 30px;
      padding: 40px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;

      .formItem_wrapper {
        width: 45%;

        &:nth-child(3) {
          width: 100%;
        }

        @media (max-width: 1024px) {
          width: 100%;
        }

        @include onMobile {
          width: 100%;
        }

        .formItem_bottomBlock {
          display: flex;
          align-items: flex-end;
          justify-content: space-between;
          width: 100%;
          flex-wrap: wrap;
          position: relative;

          .error {
            bottom: 75px;
            position: absolute;
            left: 0;

            @include onNotebook {
              bottom: 60px;
            }

            @media (min-width: 1028px) {
              bottom: -25px;
            }
          }


          @include onTablet {
            width: 100%;
          }
          @media (max-width: 1024px) {
            justify-content: flex-end;
          }

          @include onMobile {
            flex-direction: column;
          }

          div:first-child {
            width: 65%;

            @include onTablet {
              width: 45%;
            }

            @include onMobile {
              width: 100%;
              justify-content: flex-end;
            }

            @media (max-width: 1024px) {
              width: 100%;
              flex-wrap: wrap;
            }
          }

          button {
            @include onTablet {
              width: 45%;
              margin-top: 30px;
              padding: 20px 0;
            }

            @include onMobile {
              padding: 20px 0;
              width: 100%;
              margin-top: 40px;
            }
          }
        }
      }
    }

    .inheritance_item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 100%;
      margin-top: 20px;

      .inheritanceList {
        width: 100%;
        margin-top: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        span {
          font-style: normal;
          font-weight: 600;
          font-size: 18px;
          line-height: 120%;

          @include onTablet {
            font-size: 16px;
          }
          @include onMobile {
            font-size: 15px;
          }
        }

        .name {
          color: var(--blueLight);
        }

        .phone, .mail {
          color: rgba(251, 251, 251, 0.8);

          @media (max-width: 565px) {
            font-size: 14px;
          }
        }

        .contacts {
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 80%;

          @include onTablet {
            flex-direction: column;
            align-items: flex-start;
          }

          @include onMobile {
            flex-direction: column;
            align-items: flex-start;
          }

          @media (max-width: 700px) {
            width: 90%;
          }
        }

        .contactsData {
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 65%;

          @include onTablet {
            margin-top: 20px;
            width: 90%;
          }
          @include onMobile {
            flex-direction: column;
            align-items: flex-start;
            margin-top: 10px;
            width: 90%;
          }
        }
      }

      .description {
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 22px;
        color: var(--white);
      }
    }
  }
}