@import "../../../assets/style/variable";

.btnFilter {
  border-radius: 23px;
  padding: 4px 12px;
  background: transparent;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: var(--white);
  cursor: pointer;

  &:hover {
    box-shadow: 0 0 6px 1px rgba(125, 239, 255, 0.4);
  }
}

.active {
  box-shadow: 0 0 6px 1px rgba(125, 239, 255, 0.4);
  background: linear-gradient(0deg, rgba(25, 76, 99, 0.3), rgba(25, 76, 99, 0.3)), linear-gradient(55.92deg, #70E6E1 -4.23%, #6FD5FF 140.6%);
}