@import '../../assets/style/mixins';

.count {
  display: flex;
  justify-content: space-between;
  background-color: #2276A7;
  border-radius: 10px;
  align-items: center;
  overflow: hidden;
  margin-bottom: 20px;
  height: 47px;

  @include onTablet {
    margin-bottom: 10px;
  }

  @include onNotebook {
    margin-bottom: 10px;
  }

  @include onFull {
    height: 70px;
    flex: 0 0 70px;
  }

  input[type=text] {
    width: 100%;
    height: 100%;
    background-color: #2276A7;
    border: none;
    font-size: 26px;
    color: white;
    text-align: center;
  }

  input[type=text] {
    width: 100%;
    height: 100%;
    background-color: #2276A7;
    border: none;
    font-size: 26px;
    color: white;
    text-align: center;
  }

  input:focus{
    outline: none;
}

  .countIcon {
    margin-top: 5px;
    cursor: pointer;
    transition: 150ms;

    &:active {
      transform: scale(0.9);
    }

    div svg {
      width: 50px;
      height: 50px;

      @include onFull {
        width: 75px;
        height: 75px;
      }

      path {
        fill: #23BCFA;
      }
    }
  }
}