@import '../../../../assets/style/variable';

.structureItemWrapper {
  background: var(--gradienBlack);
  border-radius: 30px;
  padding: 30px 24px;

  .structureHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;

    span {
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;
      color: var(--white);

      @media screen and (max-width: 450px) {
        font-size: 14px;
      }
    }
  }

  .structureList {
    .structureListTitle {
      display: flex;
      justify-content: space-between;
      margin-top: 32px;


      span {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 120%;
        color: var(--blueLight);
        display: flex;
        align-items: center;

        &:after {
          content: '';
          width: 8px;
          height: 8px;
          display: inline-block;
          border-bottom: 1px solid var(--blueLight);
          border-right: 1px solid var(--blueLight);
          margin-left: 6px;
          transform: rotate(45deg);
          margin-bottom: 3px;
        }
      }
    }

    .structureCard {
      margin-top: 24px;

      .structureListCard {
        display: flex;
        justify-content: space-between;
        position: relative;
        padding-bottom: 14px;
        margin-top: 14px;

        &:before {
          content: '';
          width: 100%;
          display: block;
          border-bottom: solid 1px #70DDF1;
          opacity: 0.1;
          position: absolute;
          bottom: 0;
        }

        &:last-child {
          &:before {
            display: none;
          }
        }

        .line {
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 120%;
          text-transform: uppercase;
          color: var(--white);
        }

        .partners {
          font-style: normal;
          font-weight: 600;
          font-size: 14px;
          line-height: 120%;
          color: var(--white);
        }

        .reward {
          font-style: normal;
          font-weight: 600;
          font-size: 18px;
          line-height: 120%;
          color: var(--white);

          span {
            color: var(--blueDark);
            margin-right: 10px;
          }
        }
      }
    }
  }
}