  .count {
    display: flex;
    justify-content: space-between;
    background-color: #2276A7;
    border-radius: 10px;
    align-items: center;
    overflow: hidden;
    margin-bottom: 20px;
    height: 60px;

    @include onTablet {
      margin-bottom: 30px;
      height: 80px;
    }
  
    @include onNotebook {
      margin-bottom: 30px;
      height: 80px;
    }
  
    @include onFull {
      height: 80px;
      flex: 0 0 70px;
      margin-bottom: 30px;
    }

    .countIcon {
      margin-top: 5px;
      cursor: pointer;
      transition: 150ms;
      height: 100%;

      &:active {
        transform: scale(0.9);
      }

      div svg {
        width: 60px;
        height: 60px;

        @include onTablet {
