.checkBoxWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 23px;
  height: 23px;
  border: 2px solid var(--blueDark);
  border-radius: 6px;
  cursor: pointer;

  .checkBox {
    position: relative;
    margin: 0;
    opacity: 0;

    &:before {
      content: '';
      display: inline-block;
      background-repeat: no-repeat;
      background-position: center center;
      position: absolute;
    }

    &:checked::before {
      background-image: url(../../../assets/icons/Subtract.svg)
    }
    &:checked {
      opacity: 1;
      width: 100%;
      height: 100%;
    }
  }
}
}

.inputs {
  margin-bottom: 25px;
}

.inputWrapper {
  margin-bottom: 15px;

  input {
    padding: 5px;
    background: transparent;
    outline: none;
    border: none;
    border-bottom: solid 1px rgba(255, 255, 255, 0.2);
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    width: 100%;
    fill: none;

    &:empty {
      background-color: transparent;
    }
  }

  input:-webkit-autofill {
    transition: all 5000s ease-in-out 0s;
  }
}