@import '../../../assets/style/mixins';

.wrapper {
  box-sizing: border-box;

  @include onTablet {
    margin: 10px;
    flex: 1 1 50%;
  }

  @include onNotebook {
    margin: 10px;
    flex: 1 1 50%;
  }

  @include onFull {
    margin: 20px;
    flex: 1 1 50%;
  }
}

.ReferralProfit {
  color: var(--white);
  background: var(--darkBlue);
  width: 320px;
  height: 173px;
  border-radius: 15px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;

  @include onTablet {
    width: 100%;
    height: 100%;
  }
  
  @include onNotebook {
    width: 100%;
    height: 100%;
  }

  @include onFull {
    width: 100%;
    height: 100%;
    border-radius: 30px;
    padding: 30px;
  }
}

.iconBox {
  display: flex;
  width: 49px;
  height: 49px;
  border-radius: 10px;
  background: rgba(44%, 90%, 88%, 0.1);
  justify-content: center;
  align-items: center;
  margin: 0 auto 20px;

  @include onNotebook {
    margin: 0 0 20px 0;
  }

  @include onFull {
    margin: 0 0 16px 0;
    width: 70px;
    height: 70px;
  }
}

.ReferralProfitIcon {
  width: 25px;
  height: 25px;

  @include onFull {
    width: 36px;
    height: 36px;
  }

  div svg {
    @include onFull {
      width: 36px;
      height: 36px;
    }
  }
}

.BalanceIcon {
  margin-top: -12px;

  @include onFull {
    margin-top: -25px;
  }

  div{
    height: 29px;
    width: 29px;

    svg {
      height: 29px;
      width: 29px;

      @include onFull {
        width: 36px;
        height: 36px;
      }
    }
  } 

  margin-left: 10px;
}

.sum {
  display: flex;
  justify-content: center;
  font-weight: bold;
  font-size: 36px;
  margin-bottom: 5px;
  align-items: center;

  @include onNotebook {
    justify-content: start;
  }

  @include onFull {
    justify-content: start;
    align-items: center;
    font-weight: bold;
    font-size: 48px;
    line-height: 59px;
  }
}

.text {
  text-align: center;
  font-size: 12px;
  opacity: 0.75;

  @include onNotebook {
    text-align: left;
  }

  @include onFull {
    text-align: left;
    font-size: 14px;
    line-height: 17px;
  }
}

.text span {
  color: var(--white);
}