@import '../../../../assets/style/mixins';

.MobileAmount {
  flex: 0 0 24%;
}

.mobileText {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  color: rgba(255, 255, 255, 0.6);

  @include onTablet {
    display: none;
  }

  @include onNotebook {
    display: none;
  }

  @include onFull {
    display: none;
  }
}

.date {
  display: none;

  @include onTablet {
    display: flex;
  }

  @include onNotebook {
    display: flex;
  }

  @include onFull {
    display: flex;
  }
}

.amount {
  display: none;

  @include onTablet {
    display: flex;
  }

  @include onNotebook {
    display: flex;
  }

  @include onFull {
    display: flex;
  }
}

.rejectContractIcon {
  display: none;

    @include onTablet {
    display: flex;
  }

  @include onNotebook {
    display: flex;
  }

  @include onFull {
    display: flex;
  }
}

.MobileDate, .MobileAmount {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  position: relative;

  div {
    display: flex;
    justify-content: left;
  }

  @include onTablet {
    display: none;
  }

  @include onNotebook {
    display: none;
  }

  @include onFull {
    display: none;
  }
}

.MobileDate {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    display: inline-block;
    top: 15px;
    border-top: 1.5px solid rgb(100%, 100%, 100%, 0.6);
    width: 25px;
    margin: 0 -20px;
    transform: rotate(90deg);
  }
}

.rejectBox {
  cursor: pointer;
}

.item {
  width: 100%;
  height: 104px;
  background-color: #213054;
  border-radius: 15px;
  font-size: 12px;
  margin-bottom: 10px;
  color: var(--white);
  font-size: 14px;
  font-weight: bold;

  .firstPart {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 50px;

    .index {
      margin-left: 10px;
      flex: 0 0 50%;
      display: flex;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 17px;
      text-transform: uppercase;
    }

    .amount {
      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid rgb(100%, 100%, 100%, 0.6);
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }
    }
  }

  .secondPart {
    background-color: rgb(14%, 74%, 98%, 0.8);
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 54px;
    padding: 0 10px;

    .dnc {
      margin-left: 20px;
      display: flex;
      flex-direction: column;
      position: relative;

      div {
        display: flex;
      }

      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid rgb(100%, 100%, 100%, 0.6);
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }
    }

    .rzm {
      display: flex;
      flex-direction: column;
      position: relative;

      div {
        display: flex;
      }

      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid rgb(100%, 100%, 100%, 0.6);
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }
    }

    .date {
      margin-right: 20px;
      flex-direction: column;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid rgb(100%, 100%, 100%, 0.6);
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }

      .arrow {
        display: none;
      }
    }
  }
}


@include onTablet {
  .item {
    display: flex;
    height: 60px;
    font-size: 18px;
    justify-content: center;
    padding-left: 10px;

    .firstPart {
      height: 60px;
      flex: 0 0 35%;

      .index {
        display: flex;
        justify-content: left;
        flex: 0 0 50%;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        text-transform: uppercase;
      }

      .amount {
        flex: 0 0 50%;
        display: flex;
        display: flex;
        justify-content: center;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
      }
    }

    .secondPart {
      flex: 0 0 65%;
      background-color: inherit;
      align-items: center;
      justify-content: center;
      height: 60px;
      font-weight: 600;
      font-size: 16px;
      line-height: 20px;

      .dnc {
        flex: 0 0 28%;
        margin-left: 0;
        display: flex;
        flex-direction: column;

        div {
          justify-content: center;
        }

        &::before {
          content: "";
          position: absolute;
          display: inline-block;
          top: 50%;
          left: 15px;
          width: 20px;
          color: rgba(255, 255, 255, 0.2);
        }
      }

      .rzm {
        flex: 0 0 28%;
        padding-left: 10px;
        div {
          justify-content: center;
        }

        &::before {
          content: "";
          position: absolute;
          display: inline-block;
          top: 50%;
          left: 15px;
          width: 20px;
        }
      }

      .date {
        flex: 0 0 25%;
        text-align: center;
        margin-right: 0;


        &::before {
          content: "";
          position: absolute;
          display: inline-block;
          top: 50%;
          left: 10px;
          width: 20px;
        }
      }

     .rejectContract {
        display: flex;
        justify-content: center;
        flex: 0 0 15%;

        .rejectBox {
          width: 36px;
          height: 36px;
          background-color: rgb(14%, 74%, 98%, 0.15);
          border-radius: 6px;
          display: flex;
          justify-content: center;
          align-items: center;
          transition: 300ms;

          .rejectContractIcon {
            width: 14px;
            height: 14px;

            div svg {
              width: 14px;
              height: 14px;
            }
          }

          &:hover {
            background-color: rgb(14%, 74%, 98%, 0.30);
          }
        }
      }
    }
  }
}

