.rejectBlock {
  z-index: 1000;
  overflow: hidden;
}

.coin {
  margin-left: 5px;

  div svg {
    width: 14px;
    height: 14px;

    @include onTablet {
      width: 18px;
      height: 18px;
    }
  
    @include onNotebook {
