@import '../../../../assets/style/mixins';

.myLevelBlock {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 30px;
  background: var(--gradienBlack);
  border-radius: 30px;
  height: 100%;

  &_top {
    display: flex;
    align-items: center;
    padding-bottom: 15px;
    border-bottom: solid 1px rgba(35, 188, 250, 0.2);

    @include onTablet {
      justify-content: center;
    }

    .myLevelStatus {
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;

      @include onTablet {
       text-align: center;
      }

      .iconMyLevel {
        width: 100%;
        height: 100%;

        div {
          width: 100%;
          height: 100%;

          svg {
            width: 100%;
            height: 100%;
          }
        }
      }

      span {
        position: absolute;
        font-style: normal;
        font-weight: 900;
        font-size: 72px;
        line-height: 88px;
        color: var(--black);

        @include onTablet {
          font-size: 40px;
        }
      }
    }

    .aboutMy {
      margin-left: 15px;

      span, h3 {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 150%;
        color: var(--white);
      }

      span {
        opacity: 0.6
      }

      h3 {
        font-weight: 600;
        display: block;
        width: 150px;

        @include onNotebook {
          font-size: 15px;
          width: 130px;
        }

        @include onTablet {
          font-size: 16px;
        }

        @include onFull {
          font-size: 18px;
        }
      }
    }
  }

  &_body {
    margin-top: 37px;

    &_top {
      display: flex;
      align-items: center;

      span {
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;
        color: var(--blueDark);
        margin-left: 14px;
        width: 105%;

        @include onNotebook {
          font-size: 14px;
        }

        @include onFull {
          font-size: 18px;
        }
      }
    }

    &_card {
      display: flex;
      flex-direction: column;


      .title {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 150%;
        color: rgba(251, 251, 251, 0.6);
        display: flex;
        align-items: center;
        width: 150px;

        .iconInfo {
          margin-left: 6px;

          div {
            display: flex;
            align-items: center;
          }
        }
      }
    }
  }
}

.statisticLevel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 180px;

  &_leftBlock, &_rightBlock {
    display: flex;
    align-items: center;

    span {
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 150%;
      color: var(--white);
      margin-left: 8px;
    }

    .currentLevel {
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;

      .level {
        position: absolute;
        font-size: 14px;
        font-weight: 800;
        color: var(--black);
        margin: 0;
      }

      .iconMyLevelSmall {
        div {
          display: flex;
          align-items: center;
        }
      }
    }
  }
}