@import "../../assets/style/mixins";

.popup {
  position: fixed;
  width: 100%;
  height: 100vh;
  z-index: 999;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--white);
  padding: 0 20px;
  left: 0;
  background: linear-gradient(161.3deg, rgba(1, 93, 123, 0.7) -3.01%, rgba(5, 43, 67, 0.7) 46.99%, rgba(79, 50, 101, 0.7) 96.99%);
  backdrop-filter: blur(30px);

  @include onTablet {
    padding: 0 80px;
  }
}

