@mixin onFull {
  @media (min-width: 1920px) {
    @content;
  }
}

@mixin onNotebook {
  @media (min-width: 1366px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 768px) and (max-width: 1365px) {
    @content;
  }
}

@mixin upHorizontalTablet {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onMobile {
  @media (min-width: 360px) and (max-width: 767px) {
    @content;
  }
}


