@import '../../assets/style/mixins';

.historyPage {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 20px;

  @include onTablet {
    flex-direction: column;
  }

  @include onNotebook {
    flex-direction: row;
  }

  @include onFull {
    flex-direction: row;
  }

  .HistoryList {
    flex: 0 0 70%;
  }

  .Statistic {
    flex: 0 0 27%;
  }
}

.loadingPage {
  display: flex;
  flex-direction: column;
  padding-top: 200px;
  .loading {
    font-size: 30px;
    color: white;
  }
}
