@import '../../../assets/style/mixins';

.button {
  font-size: 14px;
  background: inherit;
  color: var(--white);
  border-radius: 10px;
  border: 1px solid var(--green);
  width: 100%;
  height: 100%;
  cursor: pointer;
  transition: 300ms;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 120%;
  text-align: center;
  
  @include onFull {
    border-radius: 15px;
  }

  &:hover {
    background: var(--blueDark);
  }

  &:target {
    background-color: #219DD5;
  }
}

.button span {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;

  @include onFull {
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
  }
}

.green {
  border: 1px solid var(--green);

  &:hover {
    background: var(--blueLight);
  }
}

.gold {
  border: 1px solid #F4C41A;

  &:hover {
    background: #F4C41A;
  }
}

.brown {
  border: 1px solid rgb(96%, 60%, 10%, 0.6);

  &:hover {
    background: rgb(96%, 60%, 10%, 0.6);
  }
}

.blue {
  background: transparent;
  border: none;
  width: auto;
  height: auto;
  padding: 20px 111px;

  span {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: var(--blue);
  }

  &:hover {
    background: rgba(35, 188, 250, 0.15);
  }
}
