.repeatSmsSend {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: var(--green);
  background-color: transparent;
  outline: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;

  &:hover {
    color: var(--blueDark);
  }

  .iconRepeatSmsSend {
    margin-right: 4px;

    div {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}