@import '../../../assets/style/mixins';


.info {
  margin: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #213054;
  border-radius: 15px;
  padding: 15px;

  @include onTablet {
    flex-direction: row;
    justify-content: space-between;
    padding: 10px;
  }

  @include onNotebook {
    flex-direction: row;
    justify-content: space-between;
    padding: 10px;
  }

  .closeIcon {
    align-self: flex-end;
    cursor: pointer;

    @include onTablet {
      align-self: center;
      flex-direction: row;
      order: 4;
    }

    @include onNotebook {
      align-self: center;
      flex-direction: row;
      order: 4;
    }
  }

  .mainInfoIcon {
    margin-bottom: 15px;
    cursor: pointer;

    @include onTablet {
      align-self: center;
      order: 1;
      margin-bottom: 0;
      margin-left: 20px;
    }

    @include onNotebook {
      align-self: center;
      order: 1;
      margin-bottom: 0;
      margin-left: 25px;
    }

    div svg {
      width: 41px;
      height: 41px;

      @include onTablet {
        width: 17px;
        height: 17px;
      }

      @include onNotebook {
        width: 17px;
        height: 17px;
      }

      path {
        fill: white;
      }

      &:hover {
        path {
          fill: #23BCFA
        }
      }
    }
  }

  .text {
    font-size: 14px;
    color: var(--white);
    text-align: center;
    margin-bottom: 15px;

    @include onTablet {
      flex: 0 0 60%;
      order: 2;
      margin-bottom: 0;
    }

    @include onNotebook {
      flex: 0 0 60%;
      order: 2;
      margin-bottom: 0;
    }
  }
  
  .button {
    width: 124px;
    height: 37px;
    background-color: rgba(22%, 82%, 82%, 0.2);
    color: var(--green);
    border: none;
    border-radius: 15px;
    margin-bottom: 20px;
    cursor: pointer;

    @include onTablet {
      flex: 0 0 20%;
      order: 3;
      margin-bottom: 0;
    }

    @include onNotebook {
      flex: 0 0 20%;
      order: 3;
      margin-bottom: 0;
    }
  }
}
