.registration {
  align-items: flex-end;

  .registrationForm {
    border-radius: 0 20px 20px 0;
    color: white;

    .registrationFormWrapper {

      h1 {
        font-style: normal;
        font-weight: 600;
        font-size: 40px;
        line-height: 49px;
        color: var(--white);
      }

      span {
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 22px;
      }

      form {
        .footerForm {
          justify-content: flex-end;
        }

        .inputsPhoneWrapper {
          display: flex;
          justify-content: space-between;

          div {
            width: 20%;
          }

          input {
            font-style: normal;
            font-weight: 600;
            font-size: 64px;
            line-height: 78px;
            color: #FFFFFF;
          }
        }

        .footerFormFlex {
          justify-content: space-between;

          .backBtn {
            display: flex;
            align-items: center;
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 22px;
            color: var(--green);
            background-color: transparent;
            cursor: pointer;

            .iconArrowBack {
              margin-right: 8px;
            }
          }
        }

        .footerFormCheckBox {
          display: flex;
          margin-top: 25px;

          label {
            width: fit-content;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 140%;
            margin-left: 15px;

            a {
              color: var(--blueDark);

              &:hover {
                color: var(--green);
              }
            }
          }
        }

        .checked {
          div {
            border: solid 1px red;
            position: relative;

            &:before {
              content: '';
              width: 16px;
              height: 16px;
              display: block;
              background-image: url("../../assets/icons/iconErrorWarning.svg");
              background-repeat: no-repeat;
              background-position: center;
              position: absolute;
              left: -25px;
            }
          }
        }

        .referral {
          input {
            color: #23BCFA;
          }
        }
      }
    }
  }

  .imagesBlock {
    display: flex;
    align-items: flex-end;
    margin: 0 auto;
    padding-bottom: 80px;

    svg {
      width: 100%;;
    }

    .description {
      margin-left: 60px;

      h1 {
        font-style: normal;
        font-weight: 600;
        font-size: 64px;
        line-height: 78px;
        color: var(--white);
        margin: 0;
      }

      a {
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        color: var(--green);
        display: flex;
        align-items: center;
        margin-top: 30px;

        div div {
          margin-right: 12px;

          svg path {
            fill: var(--green)
          }
        }

        &:hover {
          color: var(--blueDark);

          div div {
            svg path {
              fill: var(--blueDark)
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .registration {
    .imagesBlock {
      display: none;
    }
  }
}

@media screen and (max-width: 1500px) {
  .registration {
    .registrationForm {
      padding: 35px 35px;
    }
  }
}


