@import '../../../assets/style/mixins';

.button {
  background: var(--green);
  font-size: 18px;
  color: var(--white);
  border-radius: 10px;
  width: 100%;
  height: 100%;
  transition: 300ms;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 120%;
  text-align: center;

  @include onFull {
    border-radius: 15px;
  }

  &:hover {
    background: var(--blueDark);
  }

  &:target {
    background-color: #219DD5;
  }
}

.button span {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;

  @include onFull {
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
  }
}

.disabled {
  opacity: 0.5;
}

.green {
  background: var(--green);

  &:hover {
    background: var(--blueLight);
  }
}

.gold {
  background: rgba(96%, 77%, 10%, 0.8);;

  &:hover {
    background: rgba(96%, 77%, 10%, 1);;
  }
}

.brown {
  background: rgba(96%, 60%, 10%, 0.6);

  &:hover {
    background: rgba(96%, 60%, 10%, 0.8);
  }
}