@import '../../assets/style/mixins';

.dashboard {
  display: flex;
  flex-direction: column;
  justify-content: center;

  @include onTablet {
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 100%;
  }

  @include onNotebook {
    flex-direction: row;
    flex-wrap: wrap;
  }

}

.PanelBlock {
  display: flex;
  flex-direction: column;
  margin: 0 auto 40px;
  padding: 0 20px;
  width: 360px;

  @include onTablet {
    margin: 0 auto 10px;
    width: 100%;
    padding: 10px;
  }

  @include onNotebook {
    width: 100%;
    padding: 0;
    margin: 0 10px 20px;
    flex-direction: row;
  }

  @include onFull {
    width: 100%;
    padding: 0;
    margin: 20px 0 40px;
  }

  .balance {
    flex: 1 1 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  
    @include onTablet {
      flex-wrap: nowrap;
    }
  
    @include onNotebook {
      flex-direction: row;
      flex-wrap: nowrap;
      flex: 1 1 50%;
    }

    @include onFull {
      flex-direction: row;
      flex-wrap: nowrap;
      flex: 1 1 50%;
    }
  }
  
  .otherBlocks {
    flex: 1 1 100%;
    display: flex;
    flex-direction: column;
  
    @include onNotebook {
      display: flex;
      flex: 0 0 50%;
      flex-direction: column;
    }
  
    @include onFull {
      display: flex;
      flex: 0 0 50%;
      flex-direction: column;
    }
  
    .contracts {
      flex: 1 1 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      margin-top: 10px;
    
      @include onTablet {
        flex-wrap: nowrap;
      }
    
      @include onNotebook {
        flex-wrap: nowrap;
        flex: 1 1 50%;
      }

      @include onFull {
        flex-wrap: nowrap;
        flex: 1 1 50%;
      }
    }
    
    .other {
      flex: 1 1 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
    
      @include onTablet {
        flex-wrap: nowrap;
      }
    
      @include onNotebook {
        flex-wrap: nowrap;
        flex: 1 1 50%;
      }

      @include onFull {
        flex-wrap: nowrap;
        flex: 1 1 50%;
      }
    }
  }
}

