@import '../../assets/style/mixins';

.promoBlock {
  padding: 47px 20px 0;
  width: 100%;

  .promoList {
    display: flex;
    flex-direction: column;
    margin: 0 0 25px;
    justify-content: left;
    text-align: center;

    @media screen and (min-width: 1024px) {
      flex-direction: row;
      flex-wrap: wrap;
    }

    @include onNotebook {
      flex-direction: row;
    }
  }

  .marketingTitle {
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 22px;
    text-align: left;
    color: rgba(57, 210, 209, 1);
  }

  .marketing {
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;

    @include onTablet {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    @media screen and (min-width: 1024px) {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    @include onNotebook {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    @include onFull {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    .logoBlock {
      background: rgba(33, 48, 84, 1);
      margin-bottom: 16px;
      padding: 20px;
      border-radius: 20px;
      display: flex;
      flex: 0 0 48%;

      @media screen and (min-width: 1024px) {
        flex: 0 0 30%;
      }

      @include onNotebook {
        flex: 0 0 30%;
      }

      @include onFull {
        flex: 0 0 30%;
      }

      .iconBlock {
        background: rgba(35, 188, 250, 0.15);
        width: 92px;
        height: 92px;
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 15px;

        .marketIcon {
          margin-bottom: -3px;
        }
      }

      .contentBlock {
        display: flex;
        color:rgba(251, 251, 251, 1);
        flex: 1 1 50%;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;

        @include onFull {
          flex-direction: row;
          align-items: center;
        }

        p {
          margin: 0 0 5px;
          padding: 0;
          font-size: 18px;
          font-style: normal;
          font-weight: 500;
          line-height: 22px;
          align-self: flex-start;

          @include onFull {
            align-self: center;
          }
        }

        .buttonsBlock {
          width: 100%;
          display: flex;

          @include onFull {
            width: 50%;
            justify-content: flex-end;
          }

          .button {
            font-family: inherit;
            font-size: 18px;
            font-style: normal;
            font-weight: 500;
            line-height: 22px;
            letter-spacing: 0;
            text-align: center;
            max-width: 100px;

            margin: 0 5px;
            width: 75%;
            height: 47px;

            @include onFull {
              max-width: 135px;
            }
          }
        }
      }
    }
  }

  .firstItem {
    width: 100%;
    height: 187px;
    margin-bottom: 25px;
    cursor: pointer;
    transition: 300ms;
    border-radius: 20px;
    overflow: hidden;

    &:hover {
      box-shadow: 0 0 10px var(--blueDark);
    }

    @include onTablet {
      height: 380px;
    }

    @media screen and (min-width: 1024px) {
      height: 525px;
    }

    @include onNotebook {
      height: 492px;
    }

    @include onFull {
      height: 792px;
      border-radius: 30px;
    }

    .promoImageBlock {
      height: 100%;

      @include onNotebook {
        font-size: 28px;
        font-style: normal;
        font-weight: 800;
        line-height: 34px;

        .promoDate {
          font-size: 24px;
          font-style: normal;
          font-weight: 400;
          line-height: 31px;
        }

        .promoText {
          font-size: 24px;
          font-style: normal;
          font-weight: 400;
          line-height: 31px;
        }

        .promoTitle {
          font-size: 28px;
          font-style: normal;
          font-weight: 800;
          line-height: 34px;
        }

      }

      @include onFull {
        font-size: 34px;
        font-style: normal;
        font-weight: 800;
        line-height: 41px;

        .promoDate {
          font-size: 28px;
          font-style: normal;
          font-weight: 400;
          line-height: 36px;
        }

        .promoText {
          font-size: 28px;
          font-style: normal;
          font-weight: 400;
          line-height: 36px;
        }

        .promoTitle {
          font-size: 34px;
          font-style: normal;
          font-weight: 800;
          line-height: 41px;
        }
      }
    }
  }

  .promoItem {
    cursor: pointer;
    transition: 300ms;
    border-radius: 20px;
    overflow: hidden;
    margin: 25px;

    &:hover {
      box-shadow: 0 0 10px var(--blueDark);
    }

    @include onTablet {
      margin-bottom: 35px;

      .promoImageBlock {
        flex: 0 0 100%;
        height: 236px;
      }
    }

    @media screen and (min-width: 1024px) {
      flex: 1 1 30%;
      height: 262px;

      .promoImageBlock {
        width: 100%;
        height: 100%;
      }
    }
  }
}

.promoImageBlock {
  position: relative;
  height: 190px;
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  @media screen and (min-width: 1024px) {
    height: 262px;
  }

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.5);
    z-index: 2;
  }

  img {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
    object-fit: cover;
  }

  .promoDate {
    display: flex;
    z-index: 3;
    color: var(--white);
    height: max-content;
    margin: 20px;

    @include onTablet {
    }

    .calendarIcon {
      margin-right: 10px;

      div {
        svg {
          height: 16px;
          width: 16px;

          path {
            fill: var(--white);
          }
        }
      }
    }
  }

  .promoContent {
    color: var(--white);
    padding-left: 10px;
    position: relative;
    display: none;
    z-index: 3;
    margin: 20px;

    @include onTablet {
      display: block;
      margin-bottom: 15px;
    }

    @include onNotebook {
      display: block;
    }

    @include onFull {
      display: block;
    }

    &::after {
      content: '';
      position: absolute;
      border-color: rgba(251, 251, 251, 1);
      border-style: solid;
      border-width: 0 1px 0 0;
      border-radius: 10px;
      height: 100%;
      top: 0;
      left: 0;
      width: 1px;
      opacity: 40%;
    }

    .promoTitle {
      font-size: 18px;
      font-style: normal;
      font-weight: 600;
      line-height: 22px;
      text-align: left;
      margin: 0 0 10px;
    }

    .promoText {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
      text-align: left;
    }
  }
}

.promoMobileContent {
  color: var(--white);
  padding-left: 20px;
  margin-bottom: 35px;
  position: relative;

  @include onTablet {
    display: none;
  }

  @include onNotebook {
    display: none;
  }

  @include onFull {
    display: none;
  }

  &::after {
    content: '';
    position: absolute;
    border-color: rgba(251, 251, 251, 1);
    border-style: solid;
    border-width: 0 1px 0 0;
    border-radius: 10px;
    height: 100%;
    top: 0;
    left: 0;
    width: 1px;
    opacity: 40%;
  }

  .promoTitle {
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 22px;
    text-align: left;
  }

  .promoText {
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    text-align: left;
  }
}