@import "../../../../../assets/style/mixins";

.personalData {
  display: flex;
  flex-direction: column;

  .wrapperSelect {
    label {
      font-style: normal;
      font-weight: 500;
      font-size: 20px;
      line-height: 24px;
      color: var(--green);
      margin-bottom: 10px;
    }

    .select {
      border: 2px solid var(--green);
      box-sizing: border-box;
      border-radius: 16px;
      margin-top: 10px;
    }
  }

  &_aboutMe {
    display: flex;
    justify-content: space-between;

    @include onTablet {
      flex-direction: column;
    }

    @include onMobile {
      flex-direction: column;
    }

    .aboutMe_data {
      background: #213054;
      border-radius: 30px;
      padding: 73px 40px;
      width: 52%;
      display: flex;
      justify-content: space-between;

      @include onTablet {
        width: 100%;
      }
      @include onMobile {
        width: 100%;
        padding: 73px 20px;
      }

      @media (max-width: 565px) {
        flex-direction: column;
      }

      .myAvatar {
        display: flex;
        flex-direction: column;
        align-items: center;

        @media (max-width: 565px) {
          justify-content: space-between;
          flex-direction: row;
        }

        img {
          width: 180px;
          height: 180px;
          border-radius: 50%;
        }

        &_settings {
          display: flex;
          flex-direction: column;
          margin-top: 10px;

          button {
            position: relative;
            padding: 9px 42px;
            margin-top: 10px;
          }

          input {
            display: none;
          }
        }
      }

      .myData {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 60%;

        @include onMobile {
          flex-direction: column;
        }

        @media (max-width: 565px) {
          width: 100%;
          flex-direction: row;
        }

        .myName {
          display: flex;
          flex-direction: column;
          justify-content: center;
          width: 50%;

          @include onMobile {
            margin-top: 15px;
          }

          .title {
            font-style: normal;
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;
            color: var(--green);
          }

          .description {
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 22px;
            color: var(--white);
            opacity: 0.8;
            margin-top: 10px;
          }
        }
      }
    }

    .aboutMe_contacts {
      background: #213054;
      border-radius: 30px;
      padding: 50px 40px;
      width: 47%;

      @include onTablet {
        margin-top: 20px;
        width: 100%;
      }

      @include onMobile {
        width: 100%;
        margin-top: 20px;
        padding: 50px 20px;
      }

      button {
        margin-top: 20px;
        width: 100%;
      }
    }
  }

  &_verification {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #213054;
    border-radius: 30px;
    padding: 40px 0;
    width: 100%;
    margin-top: 40px;

    .title {
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;
      color: var(--green);
    }

    .description {
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 22px;
      text-align: center;
      color: var(--white);
      margin-top: 20px;
      margin-bottom: 34px;
      width: 60%;

      @include onMobile {
        width: 95%;
        font-size: 14px;
      }
    }

    button {
      padding: 20px 40px;
      width: 325px;
    }
  }
}