@import "../../../assets/style/mixins";


.buttons {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0 15px;

  @include onTablet {
    flex-direction: row;
    justify-content: space-around;
  }

  @include onNotebook {
    flex-direction: row;
    justify-content: space-around;
  }

  @include onFull {
    margin: 0 30px;
    flex-direction: row;
    justify-content: space-around;
  }
}

.firstButton {
  width: 280px;
  height: 47px;
  font-size: 14px;
  text-align: center;
  margin: 0 auto 15px;

  @include onTablet {
    width: 100%;
    margin: 0 10px 10px 0;
    height: 47px;
  }

  @include onNotebook {
    margin: 0 10px 10px 0;
    width: 100%;
    height: 47px;
  }

  @include onFull {
    margin: 0 10px 10px 0;
    width: 100%;
    height: 52px;
  }
}

.secondButton {
  width: 280px;
  height: 47px;
  font-size: 14px;
  text-align: center;
  margin: 0 auto 15px;

  @include onTablet {
    width: 100%;
    margin: 0 0 10px 10px;
    height: 47px;
  }

  @include onNotebook {
    margin: 0 0 10px 10px;
    width: 100%;
    height: 47px;
  }

  @include onFull {
    margin: 0 0 10px 10px;
    width: 100%;
    height: 52px;
  }
}