.topBlockWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .toggleThemeWrapper {
    display: flex;
    align-items: center;

    .currentThemeValue {
      font-style: normal;
      font-weight: 500;
      font-size: 12px;
      line-height: 15px;
      color: rgba(255, 255, 255, 0.6);
    }
  }

  .logo {
    svg {
      width: 100%;
    }
  }
}

@media screen and (max-width: 360px) {
  .topBlockWrapper {
    .currentThemeValue {
      display: none;
    }
  }
}

@media screen and (max-width: 1150px) {
  .topBlockWrapper {
    .currentThemeValue {
      margin-left: 7px;
    }
    .logo {
      svg {
        width: 95px;
        display: block;
      }
    }
  }

}

@media screen and (min-width: 768px) {

  .topBlockWrapper {
    .logo {
      svg {
        width: 102px;
      }
    }
  }

}

@media screen and (min-width: 1024px) {

  .topBlockWrapper {
    .toggleThemeWrapper{
      transform: translate(0, -2px);
    }
    .logo {
      svg {
        width: 98px;
      }
    }
  }

}

@media screen and (min-width: 1600px){

  .topBlockWrapper {
    .toggleThemeWrapper {
      transform: translate(0, -8px);
    }
    .logo {
      svg {
        width: 132px;
      }
    }
  }

}