.wrapper {
  height: 100vh;
  width: 100%;
  padding: 20px; }
  .wrapper .errorPage {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    width: 100%; }
    @media (min-width: 768px) and (max-width: 1365px) {
      .wrapper .errorPage {
        flex-wrap: nowrap;
        justify-content: space-around;
        padding: 82px 20px 0 80px; } }
    @media (min-width: 1366px) {
      .wrapper .errorPage {
        padding: 62px 20px 0 70px; } }
    @media (min-width: 1920px) {
      .wrapper .errorPage {
        padding: 82px 30px 0 140px; } }
    .wrapper .errorPage .box {
      width: 100%;
      flex: 0 0 40%;
      height: 300px;
      background: #c4c4c4;
      margin-bottom: 35px; }
    .wrapper .errorPage .textBlock {
      display: flex;
      flex-direction: column;
      align-items: center; }
      .wrapper .errorPage .textBlock .info {
        background: linear-gradient(0deg, rgba(25, 76, 99, 0.3), rgba(25, 76, 99, 0.3)), linear-gradient(55.92deg, #70E6E1 -4.23%, #6FD5FF 140.6%);
        border-radius: 7px;
        color: white;
        width: max-content;
        padding: 5px;
        margin-bottom: 20px; }
      .wrapper .errorPage .textBlock .errorCode {
        font-size: 34px;
        font-weight: 700;
        line-height: 41px;
        text-align: left;
        color: white;
        border-bottom: 1px solid #23bcfa;
        margin-bottom: 25px; }
      .wrapper .errorPage .textBlock .button {
        height: 44px;
        width: 215px;
        border-radius: 10px;
        margin: 0 auto; }
