@import '../../assets/style/mixins';

.newsPage {
  margin: 0 auto;
  overflow: hidden;
  max-width: 1920px;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;

  @include onTablet {
    padding: 82px 20px 0 80px;
  }

  @include onNotebook {
    padding: 82px 20px 0 90px;
  }

  @include onFull {
    padding: 82px 20px 0 125px;
  }

  .chosenNews {
    color: var(--white);
    font-family: Raleway;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 21px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);

    @include onTablet {
      font-size: 18px;
      line-height: 27px;
    }
  
    @include onNotebook {

    }
  
    @include onFull {
    }

    .headerBlock {
      border-radius: 15px;
      margin-bottom: 20px;

      @include onTablet {
      }
    
      @include onNotebook {

      }
    
      @include onFull {
      }

      .firstBlock {
        height: 1fr;
        padding: 15px;
        background: var(--gradient);
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;

        @include onTablet {
          display: flex;
          align-items: center;
        }
      
        @include onNotebook {
          display: flex;
          align-items: center;
        }
      
        @include onFull {
          display: flex;
          align-items: center;
        }

        .back {
          flex: 0 0 10%;
          display: flex;
          justify-content: center;
          align-items: center;
          margin-bottom: 20px;
          cursor: pointer;
          transition: 300ms;

          &:hover {
            transform: translateX(-10px);

            .iconBlock {
              background-color: var(--blueDark);
            }
          }

          @include onTablet {
            margin-bottom: 0;
            margin-left: auto;
            justify-content: start;
          }

          @include onNotebook {
            margin-bottom: 0;
            font-size: 14px;
            font-weight: 600;
            line-height: 17px;
            text-align: left;
          }
        
          @include onFull {
            margin-bottom: 0;
            font-size: 18px;
            line-height: 22px;
          }

          .iconBlock {
            height: 30px;
            width: 30px;
            margin-right: 10px;
            background: rgba(255, 255, 255, 0.1);
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 15%;
            cursor: pointer;
            transition: 300ms;

            &:hover {
              background-color: var(--blueDark);
            }

            .arrowIcon {
              margin-bottom: -2px;
            }
          }
        }

        .title {
          flex: 0 0 80%;
          text-align: center;
          @media screen and (min-width: 1024px) {
            font-size: 18px;
          }

          @include onNotebook {
            font-size: 24px;
            font-style: normal;
            font-weight: 600;
            line-height: 28px;
          }
        }
      }

      .image {
        width: 100%;
        height: 115px;

        @include onTablet {
          height: 160px;
        }

        @media screen and (min-width: 1024px) {
          height: 265px;
        }
      
        @include onNotebook {
          height: 383px;
        }
      
        @include onFull {
          height: 528px;
        }
    
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-bottom-left-radius: 15px;
          border-bottom-right-radius: 15px;
        }
      }
    }

    .text {
      width: 100%;
      font-family: Raleway;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 21px;
      letter-spacing: 0em;
      text-align: left;
      margin: auto;
      padding: 20px 0 35px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.15);

      p {
        a {
          color: var(--blueDark);
        }
      }

      @include onTablet {
        width: 670px;
      }

      @media screen and (min-width: 1024px) {
        width: 760px;
      }
    
      @include onNotebook {
        width: 828px;
      }
    
      @include onFull {
        width: 842px;
      }
    }
  }

  .infoBlock {
    width: 100%;
    margin: 0 auto;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include onTablet {
      width: 670px;
    }

    @media screen and (min-width: 1024px) {
      width: 760px;
    }
  
    @include onNotebook {
      width: 828px;
    }
  
    @include onFull {
      width: 842px;
    }

    .lastBack {
      display: flex;
      cursor: pointer;
      align-items: center;
      transition: 300ms;

      &:hover {
        transform: translateX(-10px);

        .lastIconBlock {
          background-color: var(--green);
        }

      }

      .lastIconBlock {
        width: 30px;
        height: 30px;
        background: rgba(255, 255, 255, 0.1);
        border-radius: 15%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 20px;
        transition: 300ms;

        .lastArrowIcon {
          margin-bottom: -2px;
        }
      }
    }

    .date {
      display: flex;
      color: rgba(150, 163, 171, 1);

      .calendarIcon {
        margin-right: 15px;
        
      }
    }
  }
}

.newsPage {
  .chosenNews {
    padding: 0 20px;
    margin-bottom: 30px;
    border-bottom: none;
    .headerBlock {
      .firstBlock {
        .title {
          font-size: 18px;
          line-height: 23px;
        }
      }
    }
  }
  .infoBlock{
    padding: 15px 0px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);
    .lastBack {
      .lastIconBlock{
        margin-right: 10px;
      }
    }
  }
}

@media (min-width: 768px) {

  .newsPage {
    padding: 29px 0 50px;
  }

}

@media (min-width: 1024px) {

  

}

@media (min-width: 1240px) {

  

}

@media (min-width: 1600px) {

  .newsPage {
    padding: 40px 0 50px;
    .chosenNews {
      .headerBlock {
        .firstBlock {
          padding: 26px 10px;
          .title {
            font-size: 28px;
            line-height: 34px;
          }
        }
      }
    }
  }  

}