}

@media screen and (min-width: 1600px){

  .active {
    &::before {
      height: 36%;
    }
  }

  .nav{
    width: 114px;
    &:hover {
      width: 236px;
    }
  }  

  .logo {
    height: 86px;
    .smallLogo {
      width: 30px;
      transform: translate(-50%, -20%);
    }
