      width: 130px;
      transform: translate(-60%, -15%);
    }
  }

  .list {
    .item {
      height: 70px;
      a {
        padding: 0 20px 0 40px;
      }
      .icon {
        width: 32px;
      }
      .text {
        font-size: 18px;
        padding-left: 20px;
      }
    }
  }


  .history {
    bottom: 30px;
    .historyOpenIcon {
      width: 178px;
    }
    .historyIcon {
