@import '../../../../../assets/style/variable';

.nav {
  top: 0;
  right: 0;
  width: 236px;
  height: 100vh;
  font-size: 18px;
  display: flex;
  overflow: hidden;
  transition: 300ms;
  position: absolute;
  flex-direction: column;
  background: var(--gradienBlack);
}

.logo {
  display: flex;
  padding-top: 22px;
  text-align: center;
  padding-bottom: 40px;
}

.closeMenu div {
  display: flex;
  align-items: center;
  padding-left: 56px;
}

.MobileLogoIcon {
  width: 98px;
  height: 19px;
  padding-left: 40px;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 77px;
  li{
    a{
      display: block;
    }
    a.active{
      background-color: rgb(14%, 74%, 98%, 0.15);
      border-top-left-radius: 25px;
      border-bottom-left-radius: 25px;
      box-shadow: 2px 3px 4px black;
      &::after {
        content: '';
        position: absolute;
        border-color: var(--blue);
        border-style: solid;
        border-width: 0 3px 0 0;
        border-radius: 15px;
        height: 100%;
        top: 0%;
        right: 0;
        width: 1px;
      }
      &::before {
        content: '';
        position: absolute;
        border-color: var(--blue);
        border-style: solid;
        border-width: 0 2px 0 0;
        border-radius: 10px;
        height: 60%;
        top: 20%;
        left: 0;
        width: 1px;
      }
    }
  }
}

.item {
  width: 100%;
  display: flex;
  cursor: pointer;
  text-align: left;
  position: relative;
  align-items: center;
  vertical-align: middle;
  padding: 21px 20px 20px 39px;
  div{
    width: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  svg{
    display: flex;
  }
}

