@import '../../../assets/style/mixins';

.eventList {
  display: flex;
  flex-direction: row;
  overflow: scroll;
  margin-bottom: 20px;
  padding: 20px;

  @include onTablet {
  }

  @include onNotebook {
    flex-direction: column;
    overflow: hidden;
    align-items: center;
    margin-bottom: 0;
  }

  @include onFull {
    flex-direction: column;
    overflow: hidden;
    align-items: center;
    margin-bottom: 0;
  }

  .eventItem {
    color: var(--white);
    display: flex;
    flex: 0 0 263px;
    flex-direction: column;
    justify-content: space-between;
    height: 161px;
    width: 263px;
    border-radius: 30px;
    margin-right: 20px;
    padding: 15px;
    cursor: pointer;
    transition: 300ms;
    overflow: hidden;

    &:hover {
      box-shadow: 0 0 10px var(--blueDark);
    }

    background-image: url('../icons/temporaryBackground.png');
    background-size: cover;

    @include onTablet {
    }
  
    @include onNotebook {
      width: 100%;
      margin-right: 0;
      margin-bottom: 20px;
    }
  
    @include onFull {
      width: 100%;
      margin-right: 0;
      margin-bottom: 20px;
    }

    .dateBlock {
      display: flex;

      .date {
        display: flex;
        margin-right: 25px;

        .calendarIcon {
          margin-right: 5px;
        }
      }

      .time {
        display: flex;

        .clockIcon {
          margin-right: 5px;
        }
      }
    }

    .info {
      display: flex;
      flex-direction: column;

      .type {
        width: 50px;
        height: 25px;
        line-height: 25px;
        text-align: center;
        background: var(--gradient);
        border-radius: 8px;
        margin-bottom: 10px;
      }

      .name {
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: 17px;
        letter-spacing: 0em;
        text-align: left;


        @include onFull {
          font-family: Montserrat;
          font-size: 24px;
          font-style: normal;
          font-weight: 600;
          line-height: 29px;
          letter-spacing: 0em;
          text-align: left;

        }
      }
    }
  }
}