@import '../../assets/style/variable';

.selectWrapper {
  position: relative;
  border-bottom: solid 1px rgba(255, 255, 255, 0.2);

  &:before, &:after {
    content: '';
    display: inline-block;
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
    cursor: pointer;
  }

  &:before {
    width: 16px;
    height: 20px;
    background-image: url("../../assets/icons/iconLocation.svg");
    right: 0;
  }

  &:after {
    width: 10px;
    height: 10px;
    background-image: url("../../assets/icons/iconSelectArrow.svg");
    left: 70px;
    top: 5px;
    transform: rotate(180deg);
  }
}

.optionShow {
  &:after {
    transform: rotate(0deg);
  }
}

.select {
  border-radius: 16px;
  background-color: transparent;
}

.selectedCountries {
  display: flex;
  align-items: center;

  .counter {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    color: var(--white);
  }
}

.countries {
  display: flex;
  align-items: center;
  background: #1A4A6F;
  padding: 10px 20px;
  cursor: pointer;

  &:hover {
    background: var(--green);
  }

}
  .flag {
    width: 30px;
    height: 30px;
    margin-right: 10px;
    border-radius: 50%;
  }
