@import "../../../../../assets/style/mixins";

.security {
  display: flex;
  flex-direction: column;

  @include onMobile {
    width: 100%;
    margin-left: 20px;
  }

  @media (max-width: 566px) {
    margin-left: 0;
  }

  .wrapperBlock {
    display: flex;
    justify-content: space-between;
    width: 100%;

    @media (max-width: 1254px) {
      flex-direction: column;
    }

    .wrapperItemBlock {
      width: 47%;

      @media (max-width: 1254px) {
        width: 100%;
      }

      form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        background: #213054;
        border-radius: 30px;
        padding: 40px;
        margin-top: 20px;
        height: 90%;

        @media (max-width: 566px) {
          padding: 20px;
        }

        button {
          padding: 20px;
          width: 60%;
          align-self: flex-end;
          margin-top: 40px;

          @include onTablet {
            width: 80%;
            align-self: center;
            margin-top: 20px;
          }

          @include onMobile {
            margin-top: 20px;
            width: 100%;
          }

          span {
            font-size: 18px;
          }
        }
      }
    }
  }
}

.sessionTime {
  &_block {
    margin-top: 20px;
    align-items: flex-start;


    .authentication_toggleWrapper {
      padding: 40px;
      margin-top: 20px;
      background: #213054;
      border-radius: 30px;
      display: flex;
      justify-content: space-between;

      .div {
        width: 100%;
      }
    }
  }
}

h4 {
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  color: var(--white);
  margin: 0;
}

