@font-face {
  font-family: 'fontello';
  src: url('../../../assets/style/fonts/fontello.eot');
  src: url('../../../assets/style/fonts/fontello.eot') format('embedded-opentype'),
  url('../../../assets/style/fonts/fontello.woff') format('woff'),
  url('../../../assets/style/fonts/fontello.ttf') format('truetype'),
  url('../../../assets/style/fonts/fontello.svg') format('svg');

  font-weight: normal;
  font-style: normal;
}

.inputPasswordWrapper {
  position: relative;

  img {
    right: 0;
    position: absolute;
    cursor: pointer;
  }

  input {
    font-family: "fontello";
    font-style: normal;
    font-weight: normal;

    font-variant: normal;
    text-transform: none;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    letter-spacing: 2px;

    color: var(--blueLight);
  }
}
