.formItem {
  display: flex;
  flex-direction: column;
  margin-top: 20px;

  label {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: var(--green);
    margin-bottom: 10px;
    margin-left: 10px;
  }

  .wrapperInputs {
    background: linear-gradient(269.08deg, rgba(85, 184, 190, 0.7) -33.55%, rgba(58, 119, 153, 0.7) 125.13%);
    opacity: 0.3;
    border: 2px solid #39D2D1;
    border-radius: 16px;
    padding: 18px 20px;
  }
}