@import "../../../../../../assets/style/mixins";

.exit {
  background: linear-gradient(161.3deg, rgba(1, 93, 123, 0.7) -3.01%, rgba(5, 43, 67, 0.7) 46.99%, rgba(79, 50, 101, 0.7) 96.99%);
  backdrop-filter: blur(30px);
  border-radius: 30px;
  padding: 30px 40px;
  width: 371px;
  position: absolute;
  top: 70px;
  right: 40px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;

  .location {
    display: flex;
    align-items: center;
    margin-bottom: 15px;

    .sity {
      border: 1.4px solid var(--green);
      width: 34px;
      height: 34px;
      border-radius: 50%;

      span {
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        color: var(--white)
      }
    }
  }

  .item {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: var(--white);
    opacity: 0.8;
    display: flex;
    align-items: center;
    padding: 0;

    .icon {
      margin-right: 10px;
    }
  }

  .exitBtn {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: var(--green);
    margin-top: 20px;
  }
}