@import "../../../../assets/style/mixins";

.settingsMenu {
  background: #213054;
  border-radius: 20px;
  color: white;
  height: 750px;

  @media (max-width: 955px) {
    width: 94px;
  }
  @media (max-width: 565px) {
    position: absolute;
    z-index: 9;
    top: 0;
    right: 20px;
    border: solid 0.5px var(--blueLight);
  }

  .settingsMenuItem {
    display: flex;
    flex-direction: column;
    height: 100%;
    position: relative;

    .item {
      &:last-child {
        position: absolute;
        bottom: 0;
        width: 100%;
      }

      a {
        padding: 25px 31px;
        display: flex;
        align-items: center;

        span {
          font-style: normal;
          font-weight: 500;
          font-size: 18px;
          line-height: 22px;
          color: rgba(255, 255, 255, 0.6);
          margin-left: 16px;

          @media (max-width: 966px) {
            display: none;
          }
        }
      }

      .active {
        background: rgba(35, 188, 250, 0.15);
        border-radius: 20px;

        &:before {
          content: '';
          position: absolute;
          border-color: var(--blueDark);
          border-style: solid;
          border-width: 0 3px 0 0;
          border-radius: 15px;
          height: 24px;
          right: 0;
          width: 2px;
        }

        .icon {
          div {
            svg path {
              fill-opacity: 1;
            }
          }
        }

        span {
          @include onMobile {
            position: absolute;
            top: 0;
            left: 665%;
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
            line-height: 22px;
            color: var(--white);
          }
        }
      }
    }
  }
}

