@import '../../assets/style/mixins';

.structure {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-bottom: 97px;

  @include onTablet {
    padding-bottom: 60px;
  }

  @include onMobile {
    padding-bottom: 40px;
  }

  .structureWrapper {
    width: 100%;
    padding: 25px;

    @include onTablet {
      padding: 0;
    }

    @include onMobile {
      padding: 0 20px;
    }

    .structureTopBlock {
      display: flex;
      justify-content: space-around;
      align-items: stretch;
      width: 100%;

      @include onTablet {
        flex-direction: column;
      }

      @include onMobile {
        flex-direction: column;
      }

      .rightBlockItem {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 48%;

        @include onTablet {
          width: 100%;
        }

        @include onMobile {
          flex-direction: column;
          width: 100%;
        }

        .footerCardBtn {
          margin-bottom: 20px;

          button {
            background: rgba(35, 188, 250, 0.15);
            border-radius: 15px;
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 22px;
            color: var(--blueDark);
            cursor: pointer;
            padding: 18px 51px;
            margin: 0 auto;
            display: flex;
            justify-content: center;
            align-items: center;

            &:hover {
              color: var(--gradient);
            }

            div {
              margin-right: 10px;
            }
          }
        }

        .cardWrapper {
          border-radius: 30px;
          background: #213054;
          backdrop-filter: blur(30px);
          padding: 30px;
          display: flex;
          flex-direction: column;

          .cardTop {
            display: flex;
            align-items: center;

            span {
              font-style: normal;
              font-weight: 500;
              font-size: 20px;
              line-height: 24px;
              text-align: center;
              color: #FFFFFF;
            }

            .iconCopyLink {
              background: linear-gradient(54.18deg, rgba(112, 230, 225, 0.1) -4.12%, rgba(111, 213, 255, 0.1) 136.91%);
              border-radius: 15px;
              padding: 15px 18px;
              margin-right: 19px;

              div {
                display: flex;
                justify-content: center;
                align-items: center;
              }
            }
          }

          .cardBottom {
            margin-top: 28px;

            h3 {
              font-style: normal;
              font-weight: 500;
              font-size: 14px;
              line-height: 17px;
              color: #FFFFFF;
            }

            &_block {
              padding: 15px;
              background: rgba(35, 188, 250, 0.15);
              border-radius: 10px;

              span {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 20px;
                color: var(--blueDark);
              }
            }
          }
        }

        .rightBlock, .leftBlock {
          display: flex;
          flex-direction: column;
          width: 48%;
          height: 100%;
          justify-content: space-between;

          @include onMobile {
            width: 100%;
            margin-bottom: 10px;
          }
        }

        .rightBlock {
          .DNCBalance {
            width: 100%;

            @include onTablet {
              margin-bottom: 100px;
            }

            @include onMobile {
              margin-bottom: 10px;
            }

            div:first-child {
              margin: 0;
            }
          }
        }
      }

      .leftBlockItem {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        width: 48%;

        @include onTablet {
          width: 100%;
        }

        @include onMobile {
          width: 100%;
        }
      }


      .item {
        display: flex;
        align-items: center;
        width: 100%;

        @include onMobile {
          flex-direction: column;
        }

        .NumberOfPartners {
          display: flex;
          justify-content: center;
          align-items: center;
          flex: 1 1 55%;

          @include onTablet {
            flex: 1 1 47%;
          }

          @include onMobile {
            width: 100%;
            background: #213054;
            backdrop-filter: blur(30px);
            border-radius: 20px;
            margin-bottom: 20px;
          }
        }
      }
    }

    .structureBottomBlock {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: 0 10px;

      @include onTablet {
        flex-direction: column;
      }

      @include onMobile {
        flex-direction: column;
      }

      .rightBlockItem {
        margin-top: 60px;
        width: 35%;

        @include onTablet {
          width: 100%;
        }

        @include onMobile {
          width: 100%;
          margin-top: 0;
        }
      }

      .leftBlockItem {
        margin-top: 60px;
        background: var(--gradienBlack);
        border-radius: 30px;
        padding: 32px 24px;
        width: 63%;
        height: 640px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @include onTablet {
          width: 100%;
        }

        @include onMobile {
          width: 100%;
          margin-top: 20px;
        }

        .scheduleSettings {
          display: flex;
          align-items: center;
          width: 95%;
          margin-bottom: 51px;
          position: relative;

          @media screen and (max-width: 1700px) {
            width: 100%;
          }

          @media screen and (max-width: 880px) {
            flex-direction: column;
            align-items: flex-start;
          }

          @media screen and (max-width: 675px) {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
          }

          .filerShow {
            position: absolute;
            display: flex;
            flex-direction: column;
            background: linear-gradient(161.3deg, rgba(1, 93, 123, 0.8) 0%, rgba(5, 43, 67, 0.8) 50%, rgba(79, 50, 101, 0.8) 100%);
            backdrop-filter: blur(10px);
            border-radius: 20px;
            padding: 24px 20px;
            right: -24px;
            top: -103%;
            z-index: 99;

            .filerShowBlock {
              display: flex;
              justify-content: space-between;
              align-items: center;

              button {
                margin: 0;
              }
            }

            button {
              margin-top: 20px;
            }
          }


          .title {
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 29px;
            color: var(--white);

            @media screen and (max-width: 1700px) {
              font-size: 16px;
            }
          }

          .buttonsGroup {
            display: flex;
            align-items: center;
            margin-left: 2%;

            @media screen and (max-width: 880px) {
              margin: 0;
            }

            @media screen and (max-width: 675px) {
              display: none;
            }

            button {
              margin: 0 5px;
            }
          }
        }

        .buttonsBlock {
          margin-top: 32px;
        }
      }
    }
  }
}
