@import "../../assets/style/mixins";

.recoveryWrapper {
  max-width: 1920px;
  margin: 0 auto;
  background: var(--gradienBlack);
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 60px 0 40px 0;

  .recovery {
    width: 25%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include onTablet {
      width: 40%;
    }
    @include onMobile {
      width: 100%;
    }

    .forgotPassword {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;

      h1 {
        font-style: normal;
        font-weight: 600;
        font-size: 40px;
        line-height: 49px;
        color: var(--white);
      }

      p {
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 140%;
        color: rgba(251, 251, 251, 0.8);
        text-align: center;
      }

      form {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;

        .formItem {
          display: flex;
          flex-direction: column;
          margin-top: 40px;
          width: 100%;

          label {
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 22px;
            color: rgba(255, 255, 255, 0.6)
          }
        }

        button {
          width: 100%;
          margin-top: 36px;
        }
      }

      .footerForm {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 169px;

        .title {
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 17px;
          color: rgba(255, 255, 255, 0.6)
        }

        .subTitle {
          font-style: normal;
          font-weight: 500;
          font-size: 18px;
          line-height: 22px;
          color: var(--green);
          display: flex;

          span {
            margin-left: 8px;
          }
        }
      }
    }
  }
}