@import '../../../../../assets/style/mixins';

.socialNetwork {
  background: #213054;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 40px;

  @media (max-width: 566px) {
    width: 100%;
  }

  .socialNetworkForm {
    width: 100%;
    display: flex;
    justify-content: space-between;

    @include onTablet {
      flex-direction: column;
    }

    @include onMobile {
      flex-direction: column;
    }

    .wrapperBlock {
      width: 47%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-self: center;

      @include onTablet {
        width: 100%;
      }

      @include onMobile {
        width: 100%;
      }
    }

  }

  button {
    margin-top: 40px;

    @include onMobile {
      margin-top: 20px;
      width: 100%;
    }
  }
}