@import '../../../../assets/style/mixins';

.byWarningPopup {
  background: var(--darkBlue);
  backdrop-filter: blur(30px);
  border-radius: 10px;
  padding: 13px 10px 40px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;

  @include onTablet {
    width: 690px;
    padding: 40px;
  }

  @include onNotebook {
    width: 703px;
    padding: 40px;
  }

  &::before {
    content: '';
    display: block;
    width: 150px;
    height: 7px;
    top: 0;
    z-index: 99;
    position: absolute;
    background: var(--blueLight);
    border-radius: 0 0 30px 30px;
  }

  h3 {
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    color: var(--white);
    margin-bottom: 20px;

    @include onTablet {
      font-size: 20px;
      line-height: 24px;
    }
    @include onNotebook {
      font-size: 20px;
      line-height: 24px;
    }

    @include onFull {
      font-size: 20px;
      line-height: 24px;
    }
  }

  p {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 150%;
    color: var(--white);
    text-align: center;
    margin-bottom: 30px;
    width: 100%;

    @include onTablet {
      width: 90%;
      font-size: 16px;
      line-height: 140%;
    }
    @include onNotebook {
      width: 60%;
      font-size: 16px;
      line-height: 140%;
    }

    @include onFull {
      width: 60%;
      font-size: 16px;
      line-height: 140%;
    }

    span {
      font-weight: bold;
      color: var(--blueLight);
    }
  }

  .selectOption {
    display: flex;
    align-items: center;
    width: 100%;
    margin: 10px 0;

    label {
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 140%;
      color: var(--white);
      margin-left: 10px;

      @include onTablet {
        font-size: 16px;
        line-height: 140%;
      }
      @include onNotebook {
        font-size: 16px;
        line-height: 140%;
      }
  
      @include onFull {
        font-size: 16px;
        line-height: 140%;
      }

    }
  }

  .buttonNext {
    display: flex;
    width: 100%;
    background: var(--green);
    margin-top: 40px;
    border-radius: 12px;
    height: 47px;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: var(--white);

    
    @include onTablet {
      font-size: 18px;
      line-height: 22px;
    }
    @include onNotebook {
      font-size: 18px;
      line-height: 22px;
    }

    @include onFull {
      font-size: 18px;
      line-height: 22px;
    }
  }
}