@import "./assets/style/variable.css";

body {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
}

* {
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

button {
    border: none;
    outline: none;
    cursor: pointer;
}

ul li {
    list-style: none;
}

.app {
    overflow: hidden;
    background: var(--background);
    min-height: 100vh;
    position: relative;
}