@import '../../../assets/style/mixins';

.title {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: var(--green);
  padding: 0 20px;
  margin: 22px auto 15px;
  max-width: 360px;

  @include onTablet {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    padding: 0 18px;
    margin: 30px 0 15px;
  }

  @include onNotebook {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    padding: 0 18px;
    margin: 30px 0 15px;
  }

  @include onFull {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    margin: 30px 0 17px;
  }
}

.ContractsPage {
  width: 100%;
  height: 100%;
  padding-bottom: 25px;

  @include onTablet {
    margin: 30px 0;
  }

  @include onNotebook {
  }
}

.ContractsFirstBlock {
  display: flex;
  background-color: rgb(14%, 74%, 98%, 0.15);
  color: var(--white);
  padding: 12px;
  line-height: 22px;
  font-size: 18px;
  border-radius: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
  justify-content: center;
  align-items: center;

  .contractIcon {
    margin-right: 10px;
  }
}

.contractsBlock {
  display: flex;
  flex-direction: column;
  align-items: stretch;

  @include onTablet {
    margin: 0 8px;
    flex-direction: row;
    overflow: scroll;
  }

  @include onNotebook {
    justify-content: start;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 8px;
  }

  @include onFull {
    justify-content: start;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0;
    margin-right: 20px;
  }

  .contractItem {
    flex: 1 1 0;
    margin: 0 auto;
    width: 100%;
    max-width: 320px;
    padding: 0;

    @include onTablet {
      flex: 0 0 424px;
      justify-content: start;
      flex-direction: row;
      flex-wrap: wrap;
      max-width: 424px;
      padding: 0 10px;
    }
  
    @include onNotebook {
      padding: 0 10px;
      justify-content: start;
      flex-direction: row;
      flex-wrap: wrap;
      flex: 0 0 33.33%;
      max-width: none;
    }

    @include onFull {
      justify-content: start;
      flex-direction: row;
      flex-wrap: wrap;
      flex: 0 0 33.33%;
      padding: 0 20px;
      max-width: none;

      &:last-child {
        padding: 0 10px 0 20px;
      }
    }
  }
}

.PanelBlock {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 360px;
  padding: 0 20px;

  @include onTablet {
    width: 100%;
  }

  @include onNotebook {
    width: 100%;
    flex-direction: row;
  }

  @include onFull {
    width: 100%;
  }

  .balance {
    flex: 1 1 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  
    @include onTablet {
      flex-wrap: nowrap;
    }
  
    @include onNotebook {
      flex-direction: row;
      flex-wrap: nowrap;
      flex: 1 1 50%;
    }

    @include onFull {
      flex-direction: row;
      flex-wrap: nowrap;
      flex: 1 1 50%;
    }
  }
  
  .otherBlocks {
    flex: 1 1 100%;
    display: flex;
    flex-direction: column;

    @include onTablet {
      flex-direction: row;
    }
  
    @include onNotebook {
      flex: 0 0 50%;
      flex-direction: row;
    }
  
    @include onFull {
      flex: 0 0 50%;
      flex-direction: row;
    }
  
    .contracts {
      flex: 1 1 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
    
      @include onTablet {
        flex: 0 0 50%;
      }
    
      @include onNotebook {
        flex: 1 1 50%;
      }

      @include onFull {
        flex: 1 1 50%;
      }
    }
    
    .BuyContracts {
      padding: 20px;
      flex: 1 1 100%;
    
      @include onTablet {
        flex: 0 0 50%;
      }
    
      @include onNotebook {
        flex: 1 1 50%;
      }

      @include onFull {
        flex: 1 1 50%;
      }
    }
  }
}
