.pagination {
  color: var(--white);
  display: flex;
  justify-content: center;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 29px;
  user-select: none;

  li {
    text-align: center;
    border-radius: 5px;
    margin-right: 10px;
    transition: 300ms;

    &:hover {
      color: var(--blueDark);
    }

    a {
      padding: 0 10px;
      cursor: pointer;
    }
  }

  .active {
    color: var(--blueDark);
    background: rgba(35, 188, 250, 0.2);
  }
  
  .previous {
    color: rgba(35, 188, 250, 1);

  }
}