@import '../../../../assets/style/mixins';

.goldBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #213054;
  color: var(--white);
  border-radius: 15px;
  padding: 25px 15px 15px;
  margin-bottom: 20px;
  height: 100%;

  .name {
    width: 100%;
    border-bottom: 1px solid #F4C41A;

    p {
      font-size: 12px;
      text-align: center;
      margin: 0 0 5px;
      padding: 0;
    }

    .contractName {
      justify-content: center;
      align-items: center;
      display: flex;
      margin-bottom: 10px;

      h2 {
        font-weight: bold;
        font-size: 24px;
        line-height: 29px;
        text-align: center;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        margin: 0;
        padding: 0 0 5px 18px;
        font-size: 24px;
        margin-left: 50px;
        margin-right: 50px;
      }

      .infoIcon {

        div svg {
          width: 17px;
          height: 17px;
    
          path {
            fill: #23BCFA;
          }
    
          &:hover {
            path {
              fill: #23BCFA;
            }
          }
        }
      }
    }
  }

  .description {
    width: 100%;
    padding: 20px 0 5px;
    border-bottom: 1px solid #F4C41A;

    .descriptionItem {
      display: flex;
      align-items: center;
      margin-bottom: 15px;

      p {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 150%;
        display: flex;
        align-items: center;
      }

      .iconBlock {
        flex: 0 0 36px;
        display: flex;
        width: 36px;
        height: 36px;
        border-radius: 8px;
        background: rgb(14%, 74%, 98%, 0.05);
        justify-content: center;
        align-items: center;
        margin-right: 10px;

        .descriptionIcon {
          width: 100%;
          height: 100%;

          div svg {
            width: 100%;
            height: 100%;
          }
        }
      }

      .infoIcon {
        margin-left: 10px;

        div svg {
          width: 17px;
          height: 17px;

          path {
            fill: #23BCFA;
          }

          &:hover {
            path {
              fill: #23BCFA;
            }
          }
        }
      }

      .descriptionIcon {
        div svg {
          width: 17px;
          height: 17px;

          path {
            fill: #F4C41A;
          }

          &:hover {
            path {
              fill: #F4C41A;
            }
          }
        }
      }
    }
  }

  .extraInfo {
    width: 100%;
    padding: 10px 0;
    margin-bottom: 5px;

    .firstItem {
      text-align: center;
      padding-bottom: 10px;
      border-bottom: 1px solid rgb(14%, 74%, 98%, 0.2);

      p {
        font-size: 12px;
        margin-bottom: 4px;
      }
      span {
        font-weight: bold;
        font-size: 14px;
      };
    }

    .secondItem {
      text-align: center;
      padding: 9px 0 0;

      p {
        font-size: 12px;
        margin-bottom: 5px;
      }

      .limitBlock {
        display: flex;
        justify-content: center;
        padding-left: 27px;

        span {
          font-style: normal;
          font-weight: 600;
          font-size: 14px;
          line-height: 17px;
        };

        .infoIcon {
          margin-left: 10px;
  
          div svg {
            width: 17px;
            height: 17px;
  
            path {
              fill: #23BCFA;
            }
  
            &:hover {
              path {
                fill: #23BCFA
              }
            }
          }
        }
      }
    }
  }

  .buttons {
    width: 100%;

    .button {
      width: 100%;
      height: 47px;

      &:first-child {
        margin-bottom: 10px;
      }
    }
  }
}

@include onTablet {
  .goldBlock {
    padding: 20px;
    padding-bottom: 0;

    .name {
      p {
        margin-top: 13px;
        text-align: left;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: rgba(251, 251, 251, 0.6);
      }
  
      .contractName {
        align-items: center;
        justify-content: space-between;
  
        h2 {
          display: inline-block;
          text-align: left;
          width: min-content;
          margin: 0 0 9px;
          padding: 0;
          font-style: normal;
          font-weight: bold;
          font-size: 28px;
          line-height: 34px;
          letter-spacing: 0.05em;
          text-transform: uppercase;
        }
      }
    }

    .description {
      border-bottom: none;
      margin-top: 5px;

      .descriptionItem {
        margin-bottom: 19px;
      }
    }
  
    .extraInfo {
      display: flex;
      justify-content: space-between;
      margin-bottom: 16px;
  
      .firstItem {
        padding-bottom: 0;
        border-bottom: none;
        border-left: 2px solid #F4C41A;
        text-align: left;
        padding-left: 11px;
  
        p {
          margin: 0;
          padding: 0;
          margin-bottom: 4px;
        }
        span {
          width: 0;
          padding: 0;
          font-style: normal;
          font-weight: 600;
          font-size: 14px;
          line-height: 17px;
          text-align: left;
          font-weight: bold;
          font-size: 14px;
        };
      }
  
      .secondItem {
        text-align: left;
        padding: 0;
        border-left: 2px solid #F4C41A;

        .infoIcon {
          height: 17px;
        }
  
        p {
          margin: 0 0 5px;
          padding: 0 0 0 15px;
        }
  
        .limitBlock {
          margin: 0;
          padding: 0 0 0 15px;
        }
      }
    }
  
    .buttons {
      display: flex;
      justify-content: space-between;
  
      .button {
        width: 47%;

        &:first-child {
          margin-bottom: 0;
        }
      }
    }
  }
}

