@import '../../../../assets/style/mixins';

.title {
  color: #56B6B5;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  padding: 0 20px;
  margin: 0;

  @include onTablet {
    text-align: left;
  }
  
  @include onNotebook {
    text-align: left;
  }

  @include onFull {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
  }
}

.newsList {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  margin-bottom: 30px;
  background-color: var(--background);
  max-width: 360px;

  @include onTablet {
    margin: 0 10px;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: none;
  }
  
  @include onNotebook {
    margin: 0 10px;
    flex-direction: row;
    max-width: none;
    flex-wrap: wrap;
  }

  @include onFull {
    margin: 0;
    max-width: none;
    justify-content: flex-start;
  }

  .itemWrapper {
    display: flex;
    flex-direction: column;
    margin: 0 auto 5px;
    color: var(--white);
    box-sizing: border-box;
    cursor: pointer;
    transition: 300ms;
    padding: 15px 20px 0;

    @include onTablet {
      padding: 15px 10px 0;
      flex: 0 0 50%;
    }
  
    @include onNotebook {
      flex-wrap: wrap;
      margin: 0;
      padding: 15px 10px 0;
      flex: 0 0 33.33%;
    }

     @include onFull {
       margin: 0;
      justify-content: flex-start;
      padding: 30px 20px 0;
      flex: 0 0 25%;
    }
  }
}

.yes {
  width: 320px;
  height: 47px;
  margin: 0 auto 50px;

  @include onTablet {
    width: 139px;
    margin-top: 25px;
  }

  @include onNotebook {
    margin-top: 25px;
    width: 139px;
  }

  @include onFull {
    margin-top: 25px;
    width: 195px;
    height: 62px;
  }
}

.no {
  display: none;
}
