@import '../../../assets/style/mixins';

.wrapper {
  @include onTablet {
    margin: 10px;
    flex: 1 1 50%;
  }

  @include onNotebook {
    margin: 10px;
    flex: 1 1 50%;
  }

  @include onFull {
    margin: 20px;
    flex: 1 1 50%;
  }
}

.NumberOfPartners {
  color: var(--white);
  background: var(--darkBlue);
  width: 320px;
  height: 173px;
  border-radius: 15px;
  margin: 0 auto 20px;
  padding: 20px;
  box-sizing: border-box;

  @include onTablet {
    width: 100%;
    margin: 0;
  }

  @include onNotebook {
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }

  @include onFull {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    padding: 30px;
    border-radius: 30px;
  }
}

.iconBox {
  display: flex;
  width: 49px;
  height: 49px;
  border-radius: 10px;
  background: rgba(44%, 90%, 88%, 0.1);
  justify-content: center;
  align-items: center;
  margin: 0 auto 20px;

  @include onTablet {
    margin: 0 auto 25px;
  }

  @include onNotebook {
    margin: 0 0 25px 0;
  }

  @include onFull {
    margin: 0 0 20px 0;
    width: 70px;
    height: 70px;
  }

  .partnersIcon {
    @include onTablet {
      margin-top: 3px;
    }
  
    @include onNotebook {
      margin-top: 3px;
    }
  
    @include onFull {
      margin-top: 3px;
    }

    div svg {
      @include onFull {
        width: 41px;
        height: 34px;
      }
    }
  }
}

.sum {
  display: flex;
  justify-content: center;
  font-weight: bold;
  font-size: 36px;
  margin-bottom: 8px;

  @include onNotebook {
    justify-content: flex-start;
  }

  @include onFull {
    justify-content: flex-start;
  }

  span {
    margin-top: -8px;
  }
}

.partnersPhoto {
  display: flex;
  margin-left: 45px;

  @include onFull {
    margin-left: 33px;
  }

  & .partnerPhoto {
    border-radius: 50%;
    width: 32px;
    height: 32px;
    overflow: hidden;
    transform: translateX(-20px);

    margin-left: -12px;

    @include onFull {
      margin-top: 2px;
      width: 42px;
      height: 42px;
    }

    & img {
      margin-bottom: 5px;
      width: 100%;
      height: 100%;

      @include onFull {
        text-align: left;
      }
    }
  }
}

.partnersCount {
  font-weight: bold;
  font-size: 36px;
  line-height: 44px;

  @include onFull {
    margin-top: -20px;
    font-size: 48px;
    line-height: 59px;
  }
}

.text {
  text-align: center;
  font-size: 12px;
  color: var(--white);
  opacity: 0.75;

  @include onNotebook {
    text-align: left;
  }


  @include onFull {
    text-align: left;
    font-size: 14px;
    line-height: 17px;
  }
}
