@import "../../assets/style/mixins";

.settings {
  padding: 40px 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  color: white;
  position: relative;
  align-items: flex-start;

  input {
    border: none;
  }

  @include onTablet {
    padding: 20px;
  }

  @include onMobile {
    padding: 20px;
  }

  @media (max-width: 566px) {
    flex-direction: column;
    padding: 20px;
  }

  &_leftBlock {
    width: 80%;
    display: flex;
    flex-direction: column;

    @media (max-width: 566px) {
      width: 100%;
    }

    .topBlock {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      margin-bottom: 20px;
    }

    .selectedValue {
      display: none;
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      color: var(--white);
      margin-bottom: 20px;

      @media (max-width: 955px) {
        display: block;
      }
    }

    .mobileMenu {
      display: none;
      justify-content: space-between;
      align-items: center;
      padding: 14px 35px;
      background: rgba(35, 188, 250, 0.15);
      border-radius: 10px;

      &_block {
        display: flex;
      }

      @media (max-width: 566px) {
        display: flex;
      }
    }

    .settings_wrapper {
      width: 100%;
    }
  }

  .showMenu {
    opacity: 1
  }

  .hiddenMenu {
    opacity: 0;
    z-index: -1;

    @media (min-width: 565px) {
      opacity: 1;
      z-index: 9;
    }
  }
}


