@import '../../../assets/style/mixins';

.contracts {
  z-index: 1000;
  margin: 0 auto;
  color: white;
  overflow: hidden;
  max-width: 360px;
  padding: 0 20px;

  @include onTablet {
    max-width: none;
    padding: 0 18px;
  }

  @include onNotebook {
    padding: 30px 19px 0;
    max-width: none;
  }

  @include onFull {
    padding: 0;
    max-width: none;
  }

  h2 {
    text-align: left;
    margin-left: 10px;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: var(--green);

    @include onTablet {
      margin: 11px 0 17px;
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
    }
  
    @include onNotebook {
      padding: 0;
      margin: 11px 0 17px;
      max-width: none;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
    }
  
    @include onFull {
      padding: 0;
      max-width: none;
    }
  }
}

.panel {
  display: none;
}

@include onTablet {
  .panel {
    display: flex;
    margin-bottom: 10px;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    color: rgba(255, 255, 255, 0.6);
  }

  .contractName {
    flex: 0 0 35%;
    padding-left: 25px;
  }

  .amount {
    flex: 0 0 15%;
  }

  .price {
    flex: 0 0 15%;
  }

  .income {
    flex: 0 0 15%;
  }

  .profitability {
    flex: 0 0 20%;
  }
}

@include onNotebook {
  .panel {
    display: flex;
    margin-bottom: 10px;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    color: rgba(255, 255, 255, 0.6);
  }

  .contractName {
    flex: 0 0 35%;
    padding-left: 25px;
  }

  .amount {
    flex: 0 0 15%;
  }

  .price {
    flex: 0 0 15%;
  }

  .income {
    flex: 0 0 15%;
  }

  .profitability {
    flex: 0 0 20%;
  }
}

@include onFull {
  .contracts {
    margin: 31px 20px 31px 20px;
    border-radius: 20px;

    h2 {
      font-size: 24px;
      line-height: 29px;
      margin-bottom: 23px;
    }
  }

  .panel {
    display: flex;
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: rgba(255, 255, 255, 0.6);
  }

  .contractName {
    flex: 0 0 37%;
    padding-left: 100px;
  }

  .amount {
    flex: 0 0 13%;
  }

  .price {
    flex: 0 0 15%;
  }

  .income {
    flex: 0 0 15%;
  }

  .profitability {
    flex: 0 0 20%;
  }
}