@import '../../../assets/style/mixins';

.wrapper {
  box-sizing: border-box;

  @include onTablet {
    margin: 10px;
    flex: 1 1 50%;
  }

  @include onNotebook {
    margin: 20px 10px 0;
    flex: 1 1 50%;
  }

  @include onFull {
    margin: 20px;
    flex: 1 1 50%;
  }
}

.DNCBalance {
  background: var(--gradienBlack);
  border-radius: 15px;
  margin: 0 auto 20px;
  color: var(--white);
  padding-bottom: 5px;

  @include onTablet {
    width: 100%;
    margin: 0;
    padding-bottom: 10px;
  }

  @include onNotebook {
    width: 100%;
    margin: 0 auto;
    padding-bottom: 10px;
  }

  @include onFull {
    border-radius: 30px;
    width: 100%;
    margin: 0 auto;
    padding-bottom: 20px;
  }
}

.blockName {
  display: flex;
  justify-content: center;
  margin: 0 auto 5px;
  padding-top: 27px;

  @include onFull {
    padding-top: 38px;
    margin: 0 auto 12px;
  }

  span {
    padding-top: 5px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;

    @include onFull {
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 22px;
    }
  }
}

.BalanceIcon {
  width: 18px;
  height: 18px;

  @include onFull {
    width: 28px;
    height: 28px;
  }
}

.blockName span {
  margin-left: 5px;
}

.balance {
  display: flex;
  justify-content: center;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 44px;
  margin-bottom: 5px;

  @include onFull {
    font-weight: bold;
    font-size: 56px;
    line-height: 68px;
    margin-bottom: 10px;
  }
}

.todayIncome {
  text-align: center;
  font-size: 14px;
  opacity: 0.75;

  @include onFull {
    font-size: 18px;
    line-height: 22px;
  }
}

.todayIncome span {
  font-weight: bold;
  opacity: 1;
  color: var(--blueDark);
}

//  ,    

.schedule {
  margin-top: 30px;
  text-align: center;
  font-size: 48px;
  color: var(--blueDark);

  .scheduleIcon {
    div {
      svg {
        height: 102px;
        width: 100%;

        @include onFull {
          height: 138px;
        }
      }
    }
  }
  
  .numbers {
    padding: 0 8px;
    height: 21px;
    margin-top: -30px;
    margin-bottom: 43px;

    @include onTablet {
      margin-bottom: 45px;
    }

    @include onFull {
      margin-top: -15px;
      margin-bottom: 55px;
    }

    div {
      svg {
        height: 21px;
        width: 100%;

        @include onFull {
          height: 25px;
        }
      }
    }
  }
}