@import '../../../assets/style/mixins';

.incomeBlock {
  padding: 20px;
  margin: 0;
}

.ActiveFilterIcon, .filterIcon {
  margin-left: 10px;
}

.panel {
  display: none;
  color: var(--white);
  margin-left: 20px;
  border-top: 1px solid var(--greyDark);
  padding: 20px 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.6);


  @include onTablet {
    display: flex;
  }

  @include onNotebook {
    display: flex;
  }

  @include onFull {
    display: flex;
    padding-left: 25px;
  }

  .sortByIndex {
    user-select:none;
    cursor: pointer;
    display: flex;
    flex: 1 1 26%;
  }

  .sortByAmount {
    user-select:none;
    cursor: pointer;
    display: flex;
    justify-content: left;
    flex: 1 1 24%;

    @include onTablet {
      justify-content: center;
    }

    @include onNotebook {
      justify-content: center;
    }
  }

  .sortByIncome {
    user-select:none;
    display: flex;
    cursor: pointer;
    justify-content: left;
    flex: 1 1 25%;

    @include onTablet {
      justify-content: center;
    }

    @include onNotebook {
      justify-content: center;
    }
  }

  .sortByDate {
    user-select:none;
    display: flex;
    justify-content: left;
    flex: 1 1 25%;
    cursor: pointer;

    @include onNotebook {
      justify-content: center;
    }
  }
}

.list {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;

  @include onTablet {
    padding: 0;
  }

  @include onNotebook {
    padding: 0;
  }

  @include onFull {
    padding: 0;
  }
}

.item {
  width: 100%;
  height: 104px;
  background-color: #213054;
  border-radius: 10px;
  margin-bottom: 10px;
  color: var(--white);
  font-size: 14px;
  font-weight: bold;
  margin-right: 20px;

  @include onTablet {
    .item {
      display: flex;
      height: 60px;
    }
  }

  @include onNotebook {
    .item {
      display: flex;
      height: 60px;
    }
  }

  @include onFull {
    .item {
      display: flex;
      height: 60px;
    }
  }
}

ul {
  margin: 0;
  padding: 0;
}
