.switcherWrapper {
  display: flex;
  align-items: center;
  background: transparent;
  border-radius: 20px;
  justify-content: flex-end;
  width: 44px;
  height: 24px;
  margin-left: 15px;
  border: 1px solid var(--green);
  opacity: 0.5;
  cursor: pointer;
  transform: translate(0, -1px);

  .button {
    background: transparent;
    cursor: pointer;
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;

    .icon {
      width: 26px;
      position: absolute;
      right: -2px;
      top: 0;
      transition: 200ms;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      div {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
}

.active {
  opacity: 1;

  .button {
    .icon {
      right: 18px;
      transition: 200ms;
    }
  }
}

@media screen and (min-width: 768px) {

  .switcherWrapper {
    margin-left: 10px;
    transform: translate(0, 0);
  }

}

@media screen and (min-width: 1600px){

  .switcherWrapper{
    width: 52px;
    height: 28px;
    margin-left: 15px;
    .button {
      .icon {
        width: 32px;
      }
    }
  }
  .active {
    .button {
      .icon{
        right: 21px;
      }
    }
  }

}