@import '../../assets/style/mixins';

.PartnersOpenPage {
  padding: 82px 20px 0;
  overflow: hidden;

  .imageBlock {
    background: rgba(33, 48, 84, 1);
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    overflow: hidden;
    margin-bottom: 30px;

    @include onTablet {
      flex-direction: row;
      justify-content: space-between;
      height: 147px;
    }

    @media screen and (min-width: 1024px) {
      margin-bottom: 75px;
      height: 207px;
    }
  
    @include onNotebook {
      margin-bottom: 75px;
      flex-direction: row;
      justify-content: space-between;
      height: 213px;
    }
  
    @include onFull {
      margin-bottom: 75px;
      flex-direction: row;
      justify-content: space-between;
      height: 388px;
    }

    .imageWrapper {
      width: 100%;
      height: 225px;
      border-radius: 20px;
      overflow: hidden;
      box-shadow: 0px 78.19811248779297px 78.19811248779297px -35.189151763916016px rgba(0, 0, 0, 0.21);
      box-shadow: -2px 6px 20px 0px rgba(0, 0, 0, 0.25);

      @include onTablet {
        flex: 0 0 33%;
        height: 100%;
      }
    
      @include onNotebook {
        flex: 0 0 25%;
        height: 100%;
      }
    
      @include onFull {
        flex: 0 0 28%;
        height: 100%;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .contentBlock {
      margin: 30px 5px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      width: 100%;

      p {
        color: var(--white);
        font-size: 18px;
        font-weight: 600;
        line-height: 22px;
        margin-bottom: 10px;

        @media screen and (min-width: 1024px) {
          font-size: 22px;
          line-height: 27px;
        }

        @include onNotebook {
          font-size: 28px;
          line-height: 35px;
        }

        @include onFull {
          font-size: 38px;
          line-height: 47px;
        }
      }

      span {
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 17px;
        color: var(--blueDark);

        @media screen and (min-width: 1024px) {
          flex: 0 0 33%;
          font-size: 16px;
          line-height: 20px;
        }

        @include onNotebook {
          font-size: 18px;
          line-height: 22px;
        }

        @include onFull {
          font-size: 26px;
          line-height: 32px;
        }
      }
    }
  }

  .title {
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 22px;
    text-align: center;
    color: var(--green);
    margin-bottom: 30px;

    @media screen and (min-width: 1024px) {
      text-align: left;
      margin-bottom: 50px;
      font-size: 20px;
      line-height: 24px;
    }

    @include onNotebook {
      text-align: left;
      margin-bottom: 50px;
      font-size: 20px;
      line-height: 24px;
    }

    @include onFull {
      text-align: left;
      margin-bottom: 50px;
      font-size: 32px;
      line-height: 39px;
    }
  }
  
  .iconsBlock {
    display: flex;
    flex-direction: column;
    color: var(--white);
    margin-bottom: 50px;

    @media screen and (min-width: 1024px) {
      flex-direction: row;
      justify-content: space-around;
      margin-bottom: 100px;
    }

    @include onNotebook {
      flex-direction: row;
      justify-content: space-around;
      margin-bottom: 100px;
    }

    @include onFull {
      flex-direction: row;
      justify-content: space-around;
      margin-bottom: 100px;
    }

    .iconsItem {
      display: flex;
      margin-bottom: 30px;

      @media screen and (min-width: 1024px) {
        flex: 0 0 30%;
      }

      @include onNotebook {
        flex: 0 0 30%;
      }

      @include onFull {
        flex: 0 0 30%;
      }

      .iconItem {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 70px;

        @include onNotebook {
          div svg {
            width: 64px;
            height: 64px;
          }
        }
  
        @include onFull {
          div svg {
            width: 64px;
            height: 64px;
          }
        }
      }
    }

    .contentBlock {
      margin-left: 10px;
      width: 100%;

      @media screen and (min-width: 1024px) {
        margin-left: 15px;
      }
  
      @include onNotebook {
        margin-left: 20px;
      }
  
      @include onFull {
        margin-left: 25px;
      }

      p {
        font-size: 28px;
        font-weight: 600;
        line-height: 34px;

        @include onNotebook {
          font-size: 38px;
          line-height: 47px;
        }
  
        @include onFull {
          font-size: 48px;
          line-height: 59px;
        }
      }

      span {
        font-size: 20px;
        font-weight: 400;
        line-height: 24px;

        @include onNotebook {
          font-size: 24px;
          line-height: 30px;
        }
  
        @include onFull {
          font-size: 24px;
          line-height: 30px;
        }
      }
    }
  }

  .secondTitle {

  }

  .flagsBlock {
    display: flex;
    flex-direction: column;
    margin-bottom: 60px;
    max-width: 534px;

    @media screen and (min-width: 1024px) {
      margin-bottom: 100px;
    }

    @include onNotebook {
      margin-bottom: 100px;
      justify-content: space-between;
      flex-direction: row;
      max-width: none;
    }

    @include onFull {
      margin-bottom: 100px;
      justify-content: space-between;
      flex-direction: row;
      max-width: none;
    }

    .flagsItem {
      padding: 30px 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgba(33, 48, 84, 1);
      border-radius: 10px;
      margin-bottom: 10px;
      color: var(--white);

      font-size: 12px;
      font-weight: 400;
      line-height: 15px;


      @media screen and (min-width: 1024px) {
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
      }

      @include onNotebook {
        flex: 0 0 30%;
        font-size: 18px;
        line-height: 22px;
      }

      @include onFull {
        flex: 0 0 30%;
        font-size: 20px;
        line-height: 25px;
      }

      .flagIcon {
        margin-right: 20px;

        @include onNotebook {
          div svg {
            width: 64px;
            height: 64px;
          }
        }
  
        @include onFull {
          div svg {
            width: 64px;
            height: 64px;
          }
        }
      }
    }
  }

  .firstSpecialText {
    color: var(--white);
    font-size: 12px;
    font-style: italic;
    font-weight: 700;
    line-height: 18px;
    padding-left: 10px;
    margin-bottom: 50px;
    position: relative;

    @media screen and (min-width: 1024px) {
      margin-bottom: 100px;
      font-size: 16px;
      line-height: 24px;
    }

    @include onNotebook {
      padding-left: 20px;
      margin-bottom: 100px;
      font-size: 20px;
      line-height: 30px;
    }

    @include onFull {
      padding-left: 24px;
      margin-bottom: 100px;
      font-size: 25px;
      line-height: 36px;
    }

    &::after {
      content: '';
      position: absolute;
      border-color:rgba(112, 221, 241, 1);
      border-style: solid;
      border-width: 0 4px 0 0;
      border-radius: 10px;
      height: 100%;
      top: 0;
      left: 0;
      width: 1px;
    }

    span {
      font-size: inherit;
      line-height: inherit;
      color: var(--blueDark);
    }
  }

  .lastTitle {

  }

  .secondSpecialText {
    color: var(--white);
    font-size: 12px;
    font-weight: 700;
    line-height: 18px;
    padding-left: 10px;
    margin-bottom: 30px;
    position: relative;

    @media screen and (min-width: 1024px) {
      margin-bottom: 100px;
      font-size: 16px;
      line-height: 24px;
    }

    @include onNotebook {
      padding-left: 20px;
      margin-bottom: 100px;
      font-size: 20px;
      line-height: 30px;
    }

    @include onFull {
      padding-left: 25px;
      margin-bottom: 100px;
      font-size: 24px;
      line-height: 36px;
    }

    &::after {
      content: '';
      position: absolute;
      border-color:rgba(112, 221, 241, 1);
      border-style: solid;
      border-width: 0 4px 0 0;
      border-radius: 10px;
      height: 100%;
      top: 0;
      left: 0;
      width: 1px;
    }

    p:first-child {
      margin-bottom: 20px;
    }
  }

  .lastBlock {
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
    color: var(--white);

    @media screen and (min-width: 1024px) {
      margin-bottom: 75px;
    }

    @include onNotebook {
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 75px;
    }

    @include onFull {
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 75px;
    }

    .textBlock {
      font-size: 14px;
      font-weight: 500;
      line-height: 21px;
      text-align: center;

      background: rgba(33, 48, 84, 1);
      border-radius: 10px;
      padding: 20px 10px;
      margin-bottom: 10px;

      
    @media screen and (min-width: 1024px) {
      font-size: 16px;
      line-height: 24px;
    }

      @include onNotebook {
        flex: 0 0 48%;
        font-size: 18px;
        line-height: 27px;
      }

      @include onFull {
        flex: 0 0 48%;
        font-size: 22px;
        line-height: 36px;
      }

      span {
        color: var(--blueDark);
        font-size: inherit;
        line-height: inherit;
      }

    }
  }
}
