.authorization {
  max-width: 1920px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100vh;

  .slider {
    width: 70%;
  }

  .authorizationForm {
    width: 30%;
    height: 100vh;
    padding: 62px 80px 40px 80px;
    background: var(--gradienBlack);
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .authorizationFormWrapper {
      display: flex;
      flex-direction: column;

      h1 {
        font-size: 40px;
        font-weight: 600;
        line-height: 49px;
        font-style: normal;
        color: var(--white);
      }

      span {
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 22px;
        color: rgba(251, 251, 251, 0.8);
      }
    }

    form {
      display: flex;
      flex-direction: column;

      .formItem {
        display: flex;
        flex-direction: column;
        margin-top: 50px;

        label {
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 22px;
          color: rgba(255, 255, 255, 0.6);
        }
      }

      .footerForm {
        display: flex;
        align-items: center;
        margin-top: 30px;

        .forgotPass {
          display: flex;
          align-items: center;
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 17px;
          color: var(--blueDark);
          margin-left: 30px;

          &:hover {
            color: var(--green);
          }

          &:visited {
            color: #32AFB4;
          }

          img {
            margin-right: 6px;
          }
        }
      }
    }
  }

  .authorizationFooter {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &_rightBlock {
      display: flex;
      flex-direction: column;

      img {
        margin-right: 8px;
      }
    }

    span {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 17px;
      color: rgba(255, 255, 255, 0.6);
    }

    a {
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 22px;
      color: var(--green);
      margin-top: 10px;
      display: flex;

      &:hover {
        color: var(--blueDark);

        .icon {
          div {
            svg:hover {
              path {
                fill: var(--blueDark);
              }
            }
          }
        }
      }

      .icon {
        margin-right: 6px;
      }
    }

    .iconQuestions {
      div svg {
        width: 40px;
        height: 40px;
        cursor: pointer;

        path {
          fill: rgba(35, 188, 250, 0.6);
        }

        &:hover {
          path {
            fill: #23BCFA
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1700px) {
  .authorization {
    .authorizationForm {
      padding: 44px 50px;

      .authorizationFormWrapper {
        h1 {
          font-size: 32px;
          line-height: 39px;
        }
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .authorization {
    .authorizationForm {
      padding: 35px 55px;

      form {
        .footerForm {
          flex-direction: column;

          button {
            width: 100%;
            margin-bottom: 20px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .authorization {
    .authorizationForm {
      width: 40%;
      padding: 20px 30px;

      .authorizationFormWrapper {
        h1 {
          font-size: 28px;
          line-height: 20px;
        }
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .authorization {
    .authorizationForm {
      width: 100%;
      padding: 20px 296px;

      .authorizationFormWrapper {
        span, h1 {
          text-align: center;
        }
      }
    }

      form {
        .formItem {
          label {
            text-align: center;
          }
        }
      }

      .slider {
        display: none;
      }
    }
}

@media screen and (max-width: 870px) {
  .authorization {
    .authorizationForm {
      padding: 30px 100px;
    }
  }
}

@media screen and (max-width: 570px) {
  .authorization {
    .authorizationForm {
      padding: 25px 20px;
    }
  }
}





.authorization {
  .authorizationForm {
    .authorizationContent{
      transform: translate(0, -8px);
    }
    .authorizationFormWrapper {
      padding-bottom: 12px;
      h1 {
        margin: 0;
        font-size: 24px;
        line-height: 28px;
        margin-bottom: 9px;
      }
      span {
        font-size: 14px;
        line-height: 22px;
      }
    }
    form {
      .formItem {
        margin-top: 30px;
        label {
          font-size: 14px;
          line-height: 22px;
        }
      }
      .footerForm {
        margin-top: 18px;
        button {
          font-size: 14px;
          line-height: 22px;
          padding: 13px 50px;
          border-radius: 10px;
        }
        .forgotPass {
          margin-left: 0;
          font-size: 14px;
          line-height: 18px;
          display: inline-block;
        }
      }
    }
    .authorizationFooter {
      position: relative;
      text-align: center;
      justify-content: center;
      span {
        font-size: 12px;
        line-height: 18px;
      }
      a{
        margin-top: 3px;
        font-size: 14px;
        line-height: 18px;
        .icon{
          width: 16px;
          margin-right: 4px;
          svg{
            width: 100%;
          }
        }
      }
      .iconQuestions {
        top: 50%;
        right: 0;
        z-index: 2;
        position: absolute;
        transform: translate(0, -50%);
        div {
          svg {
            width: 36px;
            height: 36px;
          }
        }
      }
    }
  }

  @media screen and (min-width: 768px){

    .authorizationForm {
      padding: 35px 160px;
      .authorizationFormWrapper {
        h1 {
          font-size: 32px;
          line-height: 36px;
          margin-bottom: 10px;
        }
      }
      form {
        .footerForm {
          button {
            width: 63%;
            margin-bottom: 20px;
          }
        }
      }
    }

  }

  @media screen and (min-width: 1024px){
    .authorizationForm {
      padding: 35px 295px;
      .authorizationContent {
        transform: translate(0, 2px);
      }
    }
  }


  @media screen and (min-width: 1025px){
    height: 100vh;
    min-height: 625px;
    .slider {
      width: 60%;
    }
    .authorizationForm {
      width: 40%;
      height: 100%;
      padding: 35px 40px;
      border-radius: 20px 0 0 20px;
      .authorizationContent {
        transform: translate(0, -3px);
      }
      form {
        .footerForm {
          margin-top: 14px;
          flex-direction: row;
          align-items: center;
          button {
            width: 40.4%;
            margin-bottom: 0;
          }
          .forgotPass {
            margin-left: 18px;
          }
        }
      }
      .authorizationFooter {
        justify-content: flex-start;
        span {
          text-align: left;
        }
      }
    }
  }

  @media screen and (min-width: 1240px){
    .slider {
      width: 65.5%;
    }
    .authorizationForm {
      width: 34.5%;
      height: 100%;
      padding: 35px 60px;
      form {
        .footerForm {
          margin-top: 14px;
          flex-direction: row;
          align-items: center;
          button {
            width: 40.4%;
            margin-bottom: 0;
          }
          .forgotPass {
            margin-left: 18px;
          }
        }
      }
      .authorizationFooter {
        justify-content: flex-start;
        span {
          text-align: left;
        }
      }
    }
  }

  @media screen and (min-width: 1600px){
    min-height: 780px;
    .slider {
      width: 68%;
    }
    .authorizationForm {
      width: 32%;
      padding: 60px 80px 38px;
      .authorizationContent {
        transform: translate(0, -6px);
      }
      .authorizationFormWrapper {
        h1{
          font-size: 40px;
          line-height: 46px;
          margin-bottom: 15px;
        }
        span {
          font-size: 18px;
          line-height: 24px;
        }
      }
      form {
        .formItem {
          margin-top: 45px;
          label {
            font-size: 18px;
            line-height: 24px;
          }
        }
        .footerForm {
          margin-top: 25px;
          button {
            font-size: 14px;
            line-height: 22px;
            padding: 20px 50px;
            border-radius: 16px;
          }
          .forgotPass {
            margin-left: 30px;
          }
        }
      }
      .authorizationFooter {
        span {
          font-size: 14px;
          line-height: 20px;
        }
        a {
          margin-top: 8px;
          font-size: 18px;
          line-height: 24px;
          .icon {
            width: 24px;
            margin-right: 6px;
          }
        }
        .iconQuestions {
          div {
            svg {
              width: 40px;
              height: 40px;
            }
          }
        }
      }
    }
  }

}