@import '../../../../assets/style/mixins';

.newsItem {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: var(--darkBlue);
  border-radius: 15px;

  &:hover {
    box-shadow: 0 0 10px var(--blueDark);
  }
}

.image {
  flex: 0 0 178px;
  height: 178px;
  width: 100%;
  border-radius: 15px;
  margin-bottom: 24px;
}

.image img {
  object-fit: cover;
  border-radius: 15px;
  width: 100%;
  height: 100%;
}

.text {
  flex: 1 1 1fr;
  padding: 0 20px;
  font-size: 14px;
  margin-bottom: 25px;
}

.title {
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 130%;
  color: var(--white);
}

.text h2 {
  margin: 0 auto;
  padding: 0;
  margin-bottom: 10px;
}

.info {
  flex: 0 0 1fr;
  display: flex;
  margin-left: 10px;
  margin-top: auto;
  padding: 10px;
}

.info span {
  margin-right: 20px;
}

.eyeIcon {
  width: 19px;
  height: 14px;
  margin-right: 8px;
}

.calendarIcon {
  width: 16px;
  height: 15px;
  margin-right: 8px;
}

@include upHorizontalTablet {
  .image {
    flex: 0 0 251px;
    height: 251px;
  }
}

@include onNotebook {
  .image {
    flex: 0 0 226px;
    height: 226px;
  }
}

@include onFull {
  .image {
    margin-bottom: 20px;
    flex: 0 0 226px;
    height: 226px;
  }

  .text {
    padding: 0 30px;
    .title {
      font-weight: 600;
      font-size: 24px;
      line-height: 130%;
    }
  }

}
