@import '../../../../assets/style/mixins';

.stepperKebab {
  //border: solid 1px red;
  margin-top: 66px;

  .stepperWrapper {
    padding: 0;

    .step {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;

      .steps_label {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        position: absolute;
        bottom: 180%;

        span {
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }

      .iconStepCheck {
        div {
          display: flex;
        }
      }
    }
  }
}

.stepperKebab {
  margin-top: 45px;
  .stepperWrapper {
    .step {
      .steps_label {
        left: 50%;
        top: -32px;
        bottom: initial;
        width: max-content;
        position: absolute;
        transform: translate(-50%, 0);
      }
    }
  }
}

