@import '../../../assets/style/mixins';

.contractListBlock {
  padding: 20px 0;
  width: 320px;
  margin: 0 auto;

  @include onTablet {
    padding: 20px;
    width: 100%;
  }

  @include onNotebook {
    padding: 20px;
    width: 100%;
  }

  @include onFull {
    padding: 20px;
    width: 100%;
  }
}

.ActiveFilterIcon, .filterIcon {
  margin-left: 10px;
}

.panel {
  display: none;
  color: var(--white);
  border-top: 1px solid var(--greyDark);
  padding: 20px 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.6);

  @include onNotebook {
    display: flex;
  }

  @include onFull {
    display: flex;
  }

  .sortByIndex {
    user-select:none;
    cursor: pointer;
    display: flex;
    flex: 0 0 20%;
    justify-content: center;
  }

  .sortByAmount {
    user-select:none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    flex: 0 0 15%;
  }

  .sortByIncomeDNC {
    user-select:none;
    display: flex;
    cursor: pointer;
    justify-content: center;
    flex: 0 0 15%;

  }

  .sortByIncomeRZM {
    user-select:none;
    display: flex;
    flex: 0 0 15%;
    justify-content: center;
    cursor: pointer;

  }

  .sortByDate {
    user-select:none;
    display: flex;
    justify-content: left;
    margin-left: 75px;
    flex: 0 0 30%;
    cursor: pointer;

    @include onNotebook {
      margin-left: 100px;
    }
  
    @include onFull {
      margin-left: 150px;
    }
  }
}

@include onTablet {
  .panel {
    display: flex;

    .sortByIndex {
      user-select:none;
      cursor: pointer;
      display: flex;
      flex: 0 0 25%;
      justify-content: center;
    }

    .sortByAmount {
      user-select:none;
      cursor: pointer;
      display: flex;
      justify-content: center;
      flex: 0 0 15%;
    }

    .sortByIncomeDNC {
      user-select:none;
      display: flex;
      cursor: pointer;
      justify-content: center;
      flex: 0 0 17%;

    }

    .sortByIncomeRZM {
      user-select:none;
      display: flex;
      flex: 0 0 17%;
      justify-content: center;
      cursor: pointer;

    }

    .sortByDate {
      user-select:none;
      display: flex;
      justify-content: left;
      flex: 0 0 26%;
      cursor: pointer;
      margin-left: 5px;
    }
  }
}

