}

@include onFull {
}

.list {
  width: 100%;
  margin: 0 auto;
}

.item {
  width: 100%;
  background-color: #213054;
  border-radius: 15px;
  font-size: 12px;
  margin-bottom: 10px;
  color: var(--white);
  font-size: 14px;
  font-weight: bold;
  margin-right: 20px;

  @include onTablet {
    .item {
      display: flex;
      height: 60px;
    }
  }

  @include onNotebook {
    .item {
      display: flex;
      height: 60px;
    }
  }

  @include onFull {
    .item {
      display: flex;
      height: 60px;
    }
  }
}

ul {
  margin: 0;
  padding: 0;
}
