@import '../../../assets/style/mixins';

.item {
  display: flex;
  flex-direction: column;
  border-radius: 15px;
  background: var(--darkBlue);
  color: var(--white);
  box-sizing: border-box;
  cursor: pointer;
  transition: 300ms;
  height: 100%;

  .image {
    height: 180px;
    width: 100%;
    border-radius: 15px;

    @include onTablet {
      height: 180px;
    }

    @include upHorizontalTablet {
      height: 227px;
    }

    @include onFull {
      height: 308px;
    }

    img {
      object-fit: cover;
      border-radius: 15px;
      width: 100%;
      height: 100%;
    }
  }

  .content {
    padding: 15px 20px;
    flex: 0 0 1fr;
    margin-bottom: 40px;

    @include onFull {
      padding: 30px 30px;
    }
    
    h2 {
      font-size: 18px;
      margin: 0;
      margin-bottom: 15px;

      @include onFull {
        font-size: 28px;
        line-height: 40px;
      }
    }

    p {
      font-size: 14px;
      line-height: 15px;

      @include onFull {
        font-size: 18px;
        line-height: 25px;
        margin-top: auto;
      }
    }
  }

  .buttons {
    padding: 0 40px;
    margin-top: auto;

    .infoButton {
      width: 100%;
      height: 47px;
      margin: 0 auto 20px;
    }

    .linkButton {
      display: none;
    }
  }
}
