@import '../../../../assets/style/mixins';

.wrapper {
  width: 100%;

  @include onTablet {
    margin: 10px;
    flex: 1 1 45%;
  }

  @include onNotebook {
    margin: 10px 10px 10px;
    flex: 1 1 50%;
  }

  @include onFull {
    margin: 20px;
    flex: 1 1 50%;
  }
}

.TotalContracts {
  color: var(--white);
  background: var(--darkBlue);
  border-radius: 15px;
  margin: 0 auto 20px;
  padding: 20px;

  @include onTablet {
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }

  @include onNotebook {
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }

  @include onFull {
    padding: 30px;
    border-radius: 30px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }
}

.iconBox {
  display: flex;
  width: 49px;
  height: 49px;
  border-radius: 10px;
  background: rgba(44%, 90%, 88%, 0.1);
  justify-content: center;
  align-items: center;
  margin: 0 auto 20px;

  @include onNotebook {
    margin: 0 0 20px 0;
  }

  @include onFull {
    width: 70px;
    height: 70px;
    margin: 0 0 16px 0;
  }
}

.notebookIcon {
  width: 19px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;

  @include onTablet {
    margin-top: 3px;
  }

  @include onNotebook {
    margin-top: 3px;
  }

  @include onFull {
    margin-top: 3px;

  }
  div svg {
    @include onFull {
      width: 30px;
      height: 38px;
    }
  }
}

.currencyIcon {
  width: 29px;
  height: 29px;
  margin-left: 10px;
}

.sum {
  display: flex;
  justify-content: center;
  font-weight: bold;
  font-size: 36px;
  margin-bottom: 5px;

  @include onNotebook {
    justify-content: start;
  }

  @include onFull {
    justify-content: start;
    font-weight: bold;
    font-size: 48px;
    line-height: 59px;
  }
}

.text {
  text-align: center;
  font-size: 12px;
  opacity: 0.75;

  @include onNotebook {
    text-align: left;
  }

  @include onFull {
    text-align: left;
    font-size: 14px;
    line-height: 17px;
  }
}

.text span {
  color: var(--white);
}