@import '../../../assets/style/mixins';

.Statistic {
  width: 100%;
  height: 100%;

  .title {
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 22px;
    color: rgba(57, 210, 209, 1);

  }

  .diagramBlock {
    background: rgba(33, 48, 84, 1);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    color: var(--white);
    align-items: center;

    @include onTablet {
      padding: 40px;
    }
  
    @include onNotebook {
      padding: 40px;
    }
  
    @include onFull {
      padding: 40px;
    }

    .diagram {
      align-content: center;
      justify-content: center;
      margin: 0 auto;
      width: 300px;
      height: 300px;

      @include onTablet {
        margin-bottom: 50px;
      }
    
      @include onNotebook {
        margin-bottom: 50px;
      }
    
      @include onFull {
        margin-bottom: 50px;
      }

      .PieChart {
        svg {
          width: 300px;
          height: 300px;
            text {
              fill: black;
            }
            path {
              stroke:black;
            }
        }
      }
    }

    .diagramList {
      width: 100%;
      margin: 0;

      .diagramItem {
        display: flex;
        justify-content: space-between;
        margin-bottom: 15px;
      }
    }
  }

  .diagramList {
    padding: 0 40px;

    .diagramItem {

      .itemName {
        display: flex;

        .Icon {
          margin-right: 10px;
        }
      }
    }
  }
}
