@import '../../../assets/style/mixins';

.wrapper {
  height: 98%;
  width: 100%;
  background: rgba(33, 48, 84, 1);
  padding-top: 10px;
  margin-top: 10px;
  border-top-right-radius: 30px;
  border-top-left-radius: 30px;
  user-select: none;
}

.calendar {
  color: var(--white);
  padding: 20px;

  @include onTablet {
    position: static;
    width: 29%;
    height: 100%;
    padding-bottom: 10px;
  }

  @include onNotebook {
    height: 83vh;
    position: fixed;
  }

  @include onFull {
    height: 83vh;
    position: fixed;
  }
}

.desktopInfo {
  display: none;

  @include onTablet {
    display: block;
  }

  @include onNotebook {
    display: block;
  }

  @include onFull {
    display: block;
  }
}

.mobileInfo {
  display: block;

  @include onTablet {
    display: none;
  }

  @include onNotebook {
    display: none;
  }

  @include onFull {
    display: none;
  }

  .mobileEvent {
    position: absolute;
    top: 30px;
    left: 17px;
    width: 10px;
    height: 10px;
    background-color: var(--blueDark);
    border-radius: 50%;
    margin-bottom: 5px;
  }
}

.header {
  height: auto;
  padding-bottom: 10px;
}

.calendar .calendarHeader {
  display: flex;
  justify-content: space-between;
}

.leftPanel {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include onTablet {
    flex-direction: row;
  }

  @include onNotebook {
    flex-direction: row;
  }

  @include onFull {
    flex-direction: row;
  }
}

.RightPanel {
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-end;
  user-select: none;

  font-size: 18px;
  font-weight: 600;
  line-height: 22px;
  color: rgba(35, 188, 250, 1);

  @include onTablet {
    flex-direction: row;
    font-size: 24px;
    font-weight: 600;
    line-height: 29px;
  }

  @include onNotebook {
    flex-direction: row;
    font-size: 24px;
    font-weight: 600;
    line-height: 29px;
  }

  @include onFull {
    flex-direction: row;
    font-size: 24px;
    font-weight: 600;
    line-height: 29px;
  }

  .dateInfo {
    display: flex;

    .showMonth {
      margin-right: 10px;
    }

    .showYear {
      margin-right: 20px;
    }
  }

  .navigationBlock {
    flex: 1 1 1fr;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 10px;

    .arrowBlock {
      width: 30px;
      max-width: 30px;
      cursor: pointer;
      height: 30px;
      background: var(--darkBlue);
      border-radius: 8px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 10px;

      &:hover {
        background-color: #015D7B;
      }
    }
  }
}

.calendarTitle {
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: left;
  color: rgba(86, 182, 181, 1);
  padding-bottom: 10px;

  @include onTablet {
    margin-right: 25px;
  }

  @include onNotebook {
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 29px;
    margin-right: 25px;
  }

  @include onFull {
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 29px;
    margin-right: 25px;
  }
}

.buttonToday {
  height: 30px;
  width: 100px;
  border-radius: 10px;

  span {
    font-size: 18px;
    font-weight: 500;
    line-height: 22px;
    color: black;
  }
}

.calendar .calendarHeader button,
.calendar .calendarHeader select {
  margin: 0 4px;
}

.calendar table {
  table-layout: fixed;
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
  border-radius: 30px;
}

.calendar th {
  vertical-align: middle;
  text-align: center;
  padding-bottom: 20px;
}

.calendar .day {
  vertical-align: top;
  text-align: center;
  border: 2px solid transparent;
  transition: 300ms;
  overflow: hidden;
  position: relative;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 29px;
}

.event {
  font-size: 14px;
  transition: 300ms;
  cursor: pointer;

  &:hover {
    box-shadow: 0 0 10px var(--blueDark);
  }
}

.calendar .day.today {
  color: white;

  .todayNumber {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 75%;
    height: 25%;
    margin: 0 auto;
    background: rgba(57, 210, 209, 1);
    border-radius: 8px;
  }

}


.todayNumber {
  margin-bottom: 15px;
}

.event {
  background: var(--gradienBlack);
  border-radius: 0 15px 15px 0;
  width: 100%;
  position: relative;
  margin-bottom: 10px;

  &::after {
    content: '';
    position: absolute;
    border-color: var(--blueDark);
    border-style: solid;
    border-width: 0 2px 0 0;
    border-radius: 10px;
    height: 100%;
    top: 0;
    left: 0;
    width: 1px;
  }

  .time {
    margin: 0;
    margin-bottom: 5px;
    padding: 0;
    line-height: 20px;
    font-size: 12px;
    font-weight: 400;

    color: rgba(255, 255, 255, 0.6);
  }

  .name {
    margin: 0;
    padding: 0;
    padding-bottom: 5px;
    font-size: 14px;
    font-weight: 600;
    line-height: 17px;
  }
}


