@import '../../../assets/style/mixins';
@import '../../contacts/ContractList/ContractItem/ContractItem.module.scss';
@import '../../../ui/pagination/pagination.module.scss';

@include onFull {
  .item {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;
  }
}

.filterPanel {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .title {
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 29px;
    text-align: left;
    color: rgba(57, 210, 209, 1);
    margin-left: 25px;
  }

  .filterName {
    display: flex;
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 22px;
    color: rgba(57, 210, 209, 1);

    @include onTablet {
      display: none;
    }
  
    @include onNotebook {
      display: none;
    }
  
    @include onFull {
      display: none;
    }

    .FilterIcon {
      margin-right: 15px;
    }
  }

  .filterList {
    display: none;
    color: white;
    user-select: none;


    @include onTablet {
      display: flex;
    }
  
    @include onNotebook {
      display: flex;
    }
  
    @include onFull {
      display: flex;
    }

    .filterItem {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 5px;
      margin-right: 20px;
      cursor: pointer;
      border-radius: 5px;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 17px;
      text-align: left;

      transition: 300ms;
    }

    .activeItem {
      background: var(--gradient);
    }
  }
}

.paginationBlock {
  margin: 20px 0;
}

.secondPart {
  .type {
    flex: 0 0 25%;
    text-align: center;
    font-size: 18px;
    line-height: 22px;
  }

  .status {
    flex: 0 0 25%;
    text-align: center;
  }
}


.rejectContract {
  display: none;

  @include onTablet {
    display: block;
  }

  @include onNotebook {
    display: block;
  }

  @include onFull {
    display: block;
  }
}

.secondPart {
  justify-content: space-around;
}

.panel {
  display: none;
  user-select: none;
  color: white;
  margin-bottom: 10px;
  
  @include onTablet {
    display: flex;
  }

  @include onNotebook {
    display: flex;
  }

  @include onFull {
    display: flex;
  }

  .ActiveFilterIcon, .filterIcon {
    margin-left: 15px;
  }

  .sortByIndex {
    display: flex;
    flex: 0 0 20%;
    cursor: pointer;
  }

  .sortByAmount {
    display: flex;
    flex: 0 0 15%;
    cursor: pointer;
    padding-left: 10px;
  }
  
  .sortByType {
    display: flex;
    flex: 0 0 15%;
    justify-content: center;
    cursor: pointer;
  }

  .sortByStatus {
    display: flex;
    flex: 0 0 20%;
    justify-content: center;
    cursor: pointer;
  }

  .sortByHistoryDate {
    display: flex;
    flex: 0 0 20%;
    justify-content: center;
    cursor: pointer;
  }
}

.item {
  .firstPart {
    .amount {
      position: relative;
    
      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        left: 0;
        width: 40px;
      }
    }
  }

  .secondPart {
    .type {
      display: flex;
      justify-content: center;

      .typeText {
        padding: 5px;
        font-size: 16px;
        font-style: normal;
        font-weight: 600;
        line-height: 20px;
        text-align: center;
      }
    }
  }

  .rejectContract {
    cursor: pointer;
  }
}

.errorText {
  display: block;
  font-size: 32px;
  color: white;
  text-align: center;
}

.mobilePanel {
  background: radial-gradient(44.03% 128.01% at 50% 0%, rgba(32, 43, 73, 0.9) 0%, rgba(23, 35, 66, 0.9) 100%);
  color: white;
  padding: 20px;

  .headerBlock {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 35px;

    .filterBlock {
      display: flex;

      .filterIcon{
        margin-right: 10px;
      }
    }
  }

  .mobileFilterList {
    .mobileFilterItem {
      background: linear-gradient(24.53deg, #104664 -85.37%, #063F5F 105.2%);
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 13px;
      border-radius: 5px;
      margin-bottom: 20px;
    }

    .mobileActiveItem {
      border: 2px solid rgba(35, 188, 250, 1);
    }
  }
}
