@import "../../../assets/style/variable";

.btn {
  background: linear-gradient(24.53deg, #104664 -85.37%, #063F5F 105.2%);
  border: 1px solid #05466C;
  border-radius: 12px;
  padding: 5px 10px;
  display: flex;
  align-items: center;
  cursor: pointer;

  span {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: var(--white);
    margin-left: 8px;
  }

  div {
    height: 29px;
  }
}