@import '../../assets/style/mixins';

.partnersPage {
  width: 100%;
  height: 100%;
}

.partnersList {
  padding: 0 20px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;

  .itemWrapper {
    padding: 20px;
  
    @include onTablet {
      flex: 0 0 45%;
      width: 45%;
    }
  
    @include onNotebook {
      flex-wrap: wrap;
      width: 25%;
      flex: 1 1 25%;
    }
  
    @include upHorizontalTablet {
      flex: 0 0 33.33%;
    }
  }
}
