.filter {
  display: none;
  align-items: center;
  background: transparent;


  @media screen and (max-width: 675px) {
    display: flex;
  }

  span {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: rgba(255, 255, 255, 0.6);
    margin-left: 6px;
  }
}