@import '../../../assets/style/variable';

.btn {
  background: var(--gradient);
  border-radius: 15px;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: var(--white);
  padding: 20px 50px;
  cursor: pointer;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;

  &:hover {
    background: var(--blueDark);
  }

  &:focus {
    background: var(--blueLight);
  }

  img {
    margin-right: 8px;
  }

}

.btn_disabled {
  opacity: 0.5;
}

@media screen and (max-width: 1700px) {
  .btn {
    font-size: 14px;
  }
}