@import "../../../../../assets/style/mixins";

.notifications {
  display: flex;
  flex-direction: column;

  @include onMobile {
    width: 100%;
  }

  &_socialNetwork {
    display: flex;
    align-self: center;
    justify-content: space-between;
    width: 100%;

    @media (max-width: 1900px) {
      flex-direction: column;
    }

    .blockItem {
      width: 47%;

      &:last-child {
        @media (max-width: 1900px) {
          margin-top: 20px;
        }
      }

      @media (max-width: 1900px) {
        width: 100%;
      }

      h4 {
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
        color: var(--white);

        @include onTablet {
          font-size: 20px;
        }

        @include onMobile {
          font-size: 20px;
        }
      }

      form {
        background: #213054;
        border-radius: 30px;
        padding: 40px;
        margin-top: 20px;
        display: flex;
        flex-direction: column;

        @include onTablet {
          padding: 20px;
        }

        @include onMobile {
          padding: 20px;
        }

        .formItem_wrapper {
          display: flex;
          justify-content: space-between;
          margin-top: 40px;

          @media (max-width: 950px) {
            flex-direction: column;
            margin-top: 20px;
          }

          .formItem {
            display: flex;
            align-items: center;
            margin-top: 20px;

            input[type=checkbox]:checked + div > label {
              font-weight: 500;
            }

            label {
              font-style: normal;
              font-size: 17px;
              line-height: 21px;
              color: var(--white);
              margin-left: 8px;
            }
          }
        }

        .switchBlock {
          display: flex;
          justify-content: space-between;
          align-items: center;

        }

        button {
          align-self: flex-end;
          margin-top: 40px;

          @media (max-width: 950px) {
            align-self: center;
          }

          @include onMobile {
            width: 100%;
            margin-top: 30px;
          }
        }
      }
    }
  }

  &_telegramBot {
    margin-top: 40px;
    width: 100%;

    h4 {
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;
      color: var(--green);

      @include onTablet {
        font-size: 18px;
      }

      @include onMobile {
        font-size: 18px;
      }
    }

    .instruction {
      background: #213054;
      border-radius: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 40px;
      margin-top: 20px;
      width: 100%;

      @include onTablet {
        flex-direction: column;
      }

      @include onMobile {
        flex-direction: column;
      }

      &_item {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include onTablet {
          flex-direction: column;
        }
        @include onMobile {
          flex-direction: column;
        }

        &_text {
          display: flex;
          justify-content: center;
          align-items: flex-end;
          flex-wrap: nowrap;
          .step {
            font-style: normal;
            font-weight: 500;
            font-size: 24px;
            line-height: 29px;
            color: var(--blueLight);
          }

          .title {
            font-style: normal;
            font-weight: 500;
            font-size: 17px;
            line-height: 21px;
            color: var(--white);
            margin-left: 10px;
            width: 200px;
          }
        }

        .iconLongArrow {
          display: flex;
          justify-content: center;
          align-items: center;
          padding: 0 5%;

          @include onTablet {
            //transform: rotate(90deg);

          }

          div {
            width: 100%;

            svg {
              width: 100%;
            }
          }
        }
      }
    }
  }
}