@import '../../assets/style/mixins';

.layoutWrapper {
  display: flex;
  justify-content: center;

  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 1920px;
    margin: 0 auto;
    padding-top: 82px;
    width: 100%;
    height: 100%;

    @include onTablet {
      padding: 71px 0 0 67px;
    }

    @include upHorizontalTablet {
      padding: 71px 0 0 69px;
    }
  
    @include onNotebook {
      padding: 71px 0 0 74px;
    }
  
    @include onFull {
      padding: 102px 20px 20px 136px;
    }
  }
}


@media (min-width: 1600px) {
  
  .layoutWrapper {
    .container{
      padding: 82px 20px 20px 136px;
    }
  }

}